import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/binder.css';

//import mp from'./mp.js';

class Binder extends Component {

    constructor(props) {
        super(props)
        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.charCode = this.charCode.bind(this)
        this.saveBinder = this.saveBinder.bind(this)
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
        if(event.target.id === "keyUp") {
            event.preventDefault();
            if(this.props.binder.binds[event.keyCode] !== undefined) window.setAlert("error", "Клавиша занята!");
            this.props.createKey(event.keyCode);
        } else  if(event.target.id === "keyUpUpdate") {
            event.preventDefault();
            if(this.props.binder.binds[event.keyCode] !== undefined) window.setAlert("error", "Клавиша занята!");
            this.props.updateKey(event.keyCode);
        } else if(event.keyCode === 27 && this.props.gui === "binder") {
            event.preventDefault();
            this.saveBinder();
        } else if(this.props.binder.binds[event.keyCode] !== undefined && !this.props.chat) {
            event.preventDefault();
            if(!this.props.binder.binds[event.keyCode][2]) {
                switch (this.props.binder.binds[event.keyCode][0]) {
                    case "Телефон":	if(this.props.gui === null) window.setGui("phone")
                                    break;
                    case "Биндер": 	if(!this.props.gui) window.setGui("binder")
                                    break;
                    case "Главное меню": if(this.props.gui === null) {
                                            mp.trigger("getDataControl")
                                            window.setGui("control")
                                        }   break;
                    case "Чат": 	if(!this.props.gui) window.chatAPI.activate(true);
                                    break;
                    default:break;
                }
            } else if (!this.props.gui) {
                if(this.props.binder.binds[event.keyCode][1]) window.chatAPI.send(this.props.binder.binds[event.which][0]);
                else {
                    window.chatAPI.activate(true);
                    window.chatAPI.value(this.props.binder.binds[event.which][0])	
                }   
            }
        } 
    }

    saveBinder() {
        mp.trigger("saveBinder", escape(JSON.stringify(this.props.binder.binds)))
		window.setAlert("success" , "Биндер успешно сохранен и задействован!");
        window.setGui(null)
    }
    
    createBind() {
        if(this.props.binder.key === null) return window.setAlert("error", "Клавиша не указана!");
        if(this.props.binder.binds[this.props.binder.key] !== undefined) return window.setAlert("error", "Клавиша занята!");
        if(this.inputMessage.value === "") return window.setAlert("error", "Сообщение пустое!");
        window.setAlert("success", "Новый бинд успешно создан!");
        this.props.creacteBind(this.inputMessage.value); 
        this.inputMessage.value = "" ; 
    }

    updateBind() {
        if(this.props.binder.binds[this.props.binder.updateKey] !== undefined && this.props.binder.updateKey !== this.props.binder.update) return window.setAlert("error", "Клавиша занята!");
        if(!this.props.binder.binds[this.props.binder.update][2]) {
            this.props.updateBind(null);
        } else {
            if(this.inputMessageUpdate.value === "") return window.setAlert("error", "Сообщение пустое!");
            this.props.updateBind(this.inputMessageUpdate.value);
        }
        window.setAlert("success", "Новый бинд успешно обновлен!");
    }

    render() { 
        return (
            this.props.gui === "binder" ?
                <div id="binder">
                    <div className="bind-header">Биндер</div>
                    <div className="bind-close" onClick={this.saveBinder}><span className="glyphicons glyphicons-remove"></span></div>
                    <table>
                        <tbody className="bind-list scroll-block">
                            <tr className="create">
                                <th><input id="keyUp" type="text" value={this.charCode(this.props.binder.key)} className="form-control" placeholder="Клавиша" readOnly/></th>
                                <th><input id="messageBind" type="text" className="form-control" ref={(input) => { if(input) { this.inputMessage = input;} }} placeholder="Текст" /></th>
                                <th><span onClick={this.props.createAuto} className={this.props.binder.auto ? "glyphicons glyphicons-ok-circle" : "glyphicons glyphicons-ban-circle" }></span></th>
                                <th><span className="glyphicons glyphicons-edit" onClick={this.createBind.bind(this)}></span></th>
                            </tr>  
                        {
                        Object.keys(this.props.binder.binds).map((key) => {
                                let [name, auto, block] = this.props.binder.binds[key]
                                return (
                                    <tr key={key} className={ this.props.binder.update === key ? "update" : "bind"}>
                                        {
                                             this.props.binder.update === key ? <th><input id="keyUpUpdate" type="text" value={this.charCode(this.props.binder.updateKey)} className="form-control" placeholder="Клавиша" readOnly/></th> : <th className="bind-key">{this.charCode(key)}</th>
                                        }
                                        {
                                            this.props.binder.update === key && block ? <th><input id="messageBindUpdate" type="text" className="form-control" defaultValue={name} ref={(input) => { if(input) { this.inputMessageUpdate = input;} }} placeholder="Текст" /></th> : <th className="bind-name">{name}</th>
                                        }
                                        {
                                            this.props.binder.update === key && block ? <th><span onClick={this.props.updateAuto} className={this.props.binder.updateAuto ? "glyphicons glyphicons-ok-circle" : "glyphicons glyphicons-ban-circle" }></span></th> : <th className="bind-auto">{auto ? <span className="glyphicons glyphicons-ok-circle"></span> : <span className="glyphicons glyphicons-ban-circle"></span> }</th>
                                        }
                                        {
                                            this.props.binder.update === key ? <th><span className="glyphicons glyphicons-edit" onClick={this.updateBind.bind(this)}></span></th>
                                            : block ? <th className="bind-block">
                                                <span className="glyphicons glyphicons-cogwheel" onClick={() => {this.props.selectUpdateBind(key)}}></span>
                                                <span className="glyphicons glyphicons-remove" onClick={() => {this.props.deleteBind(key)}}></span> 
                                            </th> : <th className="bind-block"><span style={{width:'52px'}} className="glyphicons glyphicons-cogwheel" onClick={() => {this.props.selectUpdateBind(key)}}></span></th>
                                        }
                                    </tr>    
                                )
                            })
                        } 
                        </tbody>
                    </table>
                </div>
            : <div></div>
        )
    }

    charCode(code) {
        switch (code) {
            case null: return "";
            case 179: return "MediaPlayPause";
            case 178: return "MediaStop";
            case 177: return "MediaTrackPrevious";
            case 176: return "MediaTrackNext";
            case 169: return "BrowserStop";
            case 172: return "BrowserHome";
            case 180: return "LaunchMail";
            case 171: return "BrowserFavorites";
            case 27: return "Escape";
            case 112: return "F1";
            case 113: return "F2";
            case 114: return "F3";
            case 115: return "F4"; 
            case 116: return "F5"; 
            case 117: return "F6"; 
            case 118: return "F7"; 
            case 119: return "F8"; 
            case 120: return "F9"; 
            case 121: return "F10"; 
            case 122: return "F11"; 
            case 123: return "F12"; 
            case 145: return "ScrollLock"; 
            case 192: return "`"; 
            case 49: return "1"; 
            case 50: return "2"; 
            case 51: return "3"; 
            case 52: return "4"; 
            case 53: return "5"; 
            case 54: return "6"; 
            case 55: return "7"; 
            case 56: return "8";
            case 57: return "9"; 
            case 48: return "0"; 
            case 189: return "-"; 
            case 187: return "="; 
            case 8: return "Backspace"; 
            case 45: return "Insert"; 
            case 36: return "Home"; 
            case 33: return "PageUp"; 
            case 144: return "NumLock"; 
            case 111: return "Num/"; 
            case 106: return "Num*"; 
            case 109: return "Num-";
            case 9: return "Tab";		
            case 81: return "q"; 
            case 87: return "w"; 
            case 69: return "e"; 
            case 82: return "r"; 
            case 84: return "t"; 
            case 89: return "y"; 
            case 85: return "u"; 
            case 73: return "i"; 
            case 79: return "o"; 
            case 80: return "p"; 
            case 219: return "["; 
            case 221: return "]"; 
            case 13: return "Enter";
            case 46: return "Delete"; 
            case 35: return "End"; 
            case 34: return "PageDown"; 
            case 103: return "Num7"; 
            case 104: return "Num8"; 
            case 105: return "Num9"; 
            case 107: return "Num+";
            case 20: return "CapsLock";
            case 65: return "a"; 
            case 83: return "s"; 
            case 68: return "d";
            case 70: return "f";
            case 71: return "g";
            case 72: return "h";
            case 74: return "j";
            case 75: return "k";
            case 76: return "l";
            case 186: return ";";
            case 222: return "'";
            case 38: return "ArrowUp";
            case 100: return "Num4";
            case 101: return "Num5";
            case 102: return "Num6";
            case 16: return "Shift";
            case 90: return "z";
            case 88: return "x";
            case 67: return "c";
            case 86: return "v";
            case 66: return "b";
            case 78: return "n";
            case 77: return "m";
            case 188: return ",";
            case 190: return ".";
            case 191: return "/";
            case 220: return "\\";
            case 37: return "ArrowLeft";
            case 40: return "ArrowDown";
            case 39: return "ArrowRight";
            case 97: return "Num1";
            case 98: return "Num2";
            case 99: return "Num3";
            case 17: return "Control";
            case 91: return "Meta";
            case 18: return "Alt";
            case 32: return "Space";
            case 92: return "Meta";
            case 93: return "ContextMenu";
            case 96: return "Num0";
            case 110: return "Num.";
            case 166: return "BrowserBack";
            case 167: return "BrowserForward";
            case 175: return "AudioVolumeUp";
            case 174: return "AudioVolumeDown";
            case 173: return "AudioVolumeMute";
            case 182: return "LaunchMyComputer";
            case 183: return "LaunchCalculator";
            default: return String.fromCharCode(code);
        }
    }    
}

export default connect(
    state => ({
        binder: state.binder,
        chat:   state.chat.active,
        gui: state.gui.open
    }),
    dispatch => ({
        creacteBind: (message) => {
            dispatch({ type: 'BINDER_CREATE_BIND', message});
        },
        createAuto: () => {
            dispatch({ type: 'BINDER_CREATE_AUTO'});
        },
        createKey: (key) => {
            dispatch({ type: 'BINDER_CREATE_KEY', key});
        },  
        deleteBind: (deleteKey) => {
            dispatch({ type: 'BINDER_DELETE_BIND', deleteKey });
        },
        selectUpdateBind: (updateKey) => {
            dispatch({ type: 'BINDER_SELECT_UPDATE_KEY', updateKey });
        },       
        updateKey: (updateKey) => {
            dispatch({ type: 'BINDER_UPDATE_KEY', updateKey });
        },    
        updateAuto: () => {
            dispatch({ type: 'BINDER_UPDATE_AUTO'});
        }, 
        updateBind: (message) => {
            dispatch({ type: 'BINDER_UPDATE_BIND', message });
        },        
        addMessage: (message) => {
            dispatch({ type: 'CHAT_MESSAGES', message });
        },
        clearChat: () => {
           dispatch({ type: 'CHAT_MESSAGES_CLEAR'});
        },
        showChat: (toggle) => {
         dispatch({ type: 'CHAT_SHOW', toggle });
        },        
        addHistoryMessage: (history) => {
            dispatch({ type: 'CHAT_HISTORY_MESSAGES', history });
        },       
        updatePosition: (position) => {
           dispatch({ type: 'CHAT_HISTORY_POSITION', position});
        }
    })
)(Binder);


