import React, { Component } from 'react';
import { connect } from 'react-redux';

import './css/glyphicons.min.css';
import './css/control.css';

import { control } from'./config.js';
import API from'./api.js';

//import mp from'./mp.js';

window.showDataControl = (profile, items, players) => {
    API.store.dispatch({ type: 'CONTROL_DATA_SET', profile: JSON.parse(unescape(profile)), items: JSON.parse(unescape(items)), players: JSON.parse(unescape(players))});
    window.setGui("control");
}

class Control extends Component {

    constructor(props) {
        super(props)
        this.handleKeyUp = this.handleKeyUp.bind(this)
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
        if(event.keyCode === 37)
        {
            event.preventDefault();
            this.props.setFocus(API.leftSlider(control.menu.length-1, this.props.control.focus))
        } 
        else if(event.keyCode === 39)
        {
            event.preventDefault();
            this.props.setFocus(API.rightSlider(control.menu.length-1, this.props.control.focus))
        }
        else if(event.keyCode === 38)
        {
            event.preventDefault();
            if(this.props.control.select === 2)  this.props.setFocusItem(API.leftSlider(control.settings.length-1, this.props.control.focusItem))
            else if(this.props.control.select === 3)  this.props.setFocusItem(API.leftSlider(control.help.length-1, this.props.control.focusItem))
            else if(this.props.control.select === 4)  this.props.setFocusItem(API.leftSlider(control.cmd.length-1, this.props.control.focusItem))
            else if(this.props.control.select === 5)  this.props.setFocusItem(API.leftSlider(control.rules.length-1, this.props.control.focusItem))
        } 
        else if(event.keyCode === 40)
        {
            event.preventDefault();
            if(this.props.control.select === 2)  this.props.setFocusItem(API.rightSlider(control.settings.length-1, this.props.control.focusItem))
            else if(this.props.control.select === 3)  this.props.setFocusItem(API.rightSlider(control.help.length-1, this.props.control.focusItem))
            else if(this.props.control.select === 4)  this.props.setFocusItem(API.rightSlider(control.cmd.length-1, this.props.control.focusItem))
            else if(this.props.control.select === 5)  this.props.setFocusItem(API.rightSlider(control.rules.length-1, this.props.control.focusItem))
        }        	
        else if(event.keyCode === 13) 
        {
            event.preventDefault();
            if(this.props.control.focusItem === this.props.control.selectItem && this.props.control.focus !== this.props.control.select) this.props.setSelect(this.props.control.focus)
            else this.props.setSelectItem(this.props.control.focusItem)
            
        } else if(event.keyCode === 27) {
            event.preventDefault();
            window.setGui(null)
        }  
    }

    updatePassword() {
        let password = this.inputPassword.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(password.length < 6) return window.setAlert("error", "Длина пароль должна быть не меньше 6 символов");
        mp.trigger("updatePasswordControl", password)
        this.inputPassword.value = ""
    }
    updateName() {
        let name = this.inputName.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(name.length < 3 || name.length > 30) return window.setAlert("error", "Длина игрового имени должна быть не меньше 3 и не больше 32 символов");
        if(!/^([A-Z][a-z]+_[A-Z][a-z]+)$/.test(name))  return window.setAlert("error", "Некорректное игровое имя. Пример РП никнейма: John_Dow");
        mp.trigger("updateNameControl", name)
        this.inputName.value = ""
    }
    updateEmail() {
        let email = this.inputEmail.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) return window.setAlert("error", "Некорректный адрес электронной почты. Пример: admin@ren-play.ru");
        mp.trigger("updateEmailControl", email)
        this.inputEmail.value = ""
    }        

    supportControl() { // report
        let supportMessage = this.inputSupport.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(supportMessage.length === 0) return window.setAlert("error", "Вы не можете отправить пустое сообщение");
        if(supportMessage.length > 120) return window.setAlert("error", "Длина сообщения не может быть больше 120 символов");
        mp.trigger("supportControl", supportMessage)
        this.inputSupport.value = ""
    }
    helperControl() {
        let helperMessage = this.inputHelper.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(helperMessage.length === 0) return window.setAlert("error", "Вы не можете отправить пустое сообщение");
        if(helperMessage.length > 120) return window.setAlert("error", "Длина сообщения не может быть больше 120 символов");
        mp.trigger("helperControl", helperMessage)
        this.inputHelper.value = ""
    }
    wishesControl() {
        let wishesMessage = this.inputWishes.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(wishesMessage.length === 0) return window.setAlert("error", "Вы не можете отправить пустое сообщение");
        if(wishesMessage.length > 120) return window.setAlert("error", "Длина сообщения не может быть больше 120 символов");
        mp.trigger("wishesControl", wishesMessage)
        this.inputWishes.value = ""
    }   
      

    render() {
        return (<div id="control">
                    <ul className="control-menu">
                        {
                            control.menu.map((item, key) => {
                                return (
                                    <li key={key} className={this.props.control.focus === key ? "focus" : ""} onMouseOver={() => {this.props.setFocus(key)}} onClick={() => {this.props.setSelect(key)}} >{item}</li>
                                )
                            })

                        }
                    </ul>
                    <div className="control-close" onClick={() => { window.setGui(null)} }><span className="glyphicons glyphicons-remove"></span></div>
                    {
                        this.props.control.select === 0 ? <table className="players">
                                <tbody className="scroll-block">
                                    {
                                        this.props.control.listPlayers.map(([name, color, ping], key) => {
                                            return (
                                                <tr key = {key}>
                                                    <th className="playerId">{key}</th>
                                                    <th className="playerName"><b style={{color:'rgba(' + color + ')'}}>{name}</b></th>
                                                    <th className="playerPing">{ping}</th>
                                                </tr>	
                                            )
                                        })
                                    }
                                </tbody>
                            </table>
                        : this.props.control.select === 1 ? <div className="profile">
                        {
                            <div>
                                <ul className="user">
                                <li>Игровое имя: {this.props.control.profile.name ? this.props.control.profile.name : "-"}</li>
                                <li>Пол: {this.props.control.profile.floor === 0 ? "Мужской" : "Женский"}</li>
                                <li>Опыт: {this.props.control.profile.exp ? this.props.control.profile.exp : "-"}</li>
                                <li>Смертей: {this.props.control.profile.kills ? this.props.control.profile.kills : "-"}</li>
                                <li>Убийств: {this.props.control.profile.deaths ?this.props.control.profile.deaths : "-"}</li>
                                <li>Розыск: {this.props.control.profile.offense ? this.props.control.profile.offense : "-"}</li>
                                <li>Фракция: {this.props.control.profile.faction ? this.props.control.profile.faction : "-"}</li>
                                <li>Ранг: {this.props.control.profile.rank ? this.props.control.profile.rank : "-"}</li>
                                <li>Работа: {this.props.control.profile.job ? this.props.control.profile.job : "-"}</li>
                                <li>Голод: {this.props.control.profile.satiety ? this.props.control.profile.satiety : "-"} из 100</li>
                                <li>Жажда: {this.props.control.profile.dehydration ? this.props.control.profile.dehydration : "-"} из 100</li>
                            </ul>
                            <div className="edit-items">
                            </div>
                            <ul className="items">
                                {
                                    Object.keys(this.props.control.items).map((key) => {
                                        return (
                                            <li key={key}>
                                                <img src={require(`./img/items/${key}.png`)} alt={key}/>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                            </div>
                        }

                        </div>
                        : this.props.control.select === 2 ? <div className="settings">
                                <ul className="item-menu">
                                    {
                                        control.settings.map((item, key) => {
                                            return (
                                                <li key={key} className={this.props.control.focusItem === key ? "focus" : ""} onMouseOver={() => {this.props.setFocusItem(key)}} onClick={() => {this.props.setSelectItem(key)}} >{item}</li>
                                            )
                                        })
            
                                    }
                                </ul>
                                <div className="item-body scroll-block">
                                {
                                    this.props.control.selectItem === 0 ?
                                    <div>
                                        <input ref={(input) => { if(input) { this.inputName =  input; }}} className="form-control"  type="text"/>
                                        <button type="button"  onClick={this.updateName.bind(this)} className="btn btn-primary ">Сменить игровое имя</button>
                                    </div> :
                                    this.props.control.selectItem === 1 ?    
                                    <div>
                                        <input ref={(input) => { if(input) { this.inputEmail =  input; }}} className="form-control"  type="email"/>
                                        <button type="button"  onClick={this.updateEmail.bind(this)} className="btn btn-primary ">Сменить почту</button>
                                    </div> :
                                    this.props.control.selectItem === 2 ?
                                    <div>
                                        <input ref={(input) => { if(input) { this.inputPassword =  input; }}} className="form-control"  type="password"/>
                                        <button type="button"  onClick={this.updatePassword.bind(this)} className="btn btn-primary ">Сменить пароль</button>
                                    </div> :
                                    <div>Настройка не найдена</div>   
                                }
                                </div>
                        </div>
                        : this.props.control.select === 3 ? <div className="help">
                            <ul className="item-menu">
                                {
                                    control.help.map((item, key) => {
                                        return (
                                            <li key={key} className={this.props.control.focusItem === key ? "focus" : ""} onMouseOver={() => {this.props.setFocusItem(key)}} onClick={() => {this.props.setSelectItem(key)}} >{item}</li>
                                        )
                                    })
        
                                }
                            </ul>
                            <div className="item-body scroll-block">
                                {
                                    this.props.control.selectItem === 0 ?
                                    <div>
                                        <input ref={(input) => { if(input) { this.inputHelper =  input; }}} className="form-control"  type="text"/>
                                        <button type="button"  onClick={this.helperControl.bind(this)} className="btn btn-primary ">Отправить</button>
                                    </div> :
                                    this.props.control.selectItem === 1 ?    
                                    <div>
                                        <input ref={(input) => { if(input) { this.inputSupport =  input; }}} className="form-control"  type="text"/>
                                        <button type="button"  onClick={this.supportControl.bind(this)} className="btn btn-primary ">Отправить</button>
                                    </div> :
                                    this.props.control.selectItem === 2 ?
                                    <div>
                                        <input ref={(input) => { if(input) { this.inputWishes =  input; }}} className="form-control"  type="text"/>
                                        <button type="button"  onClick={this.wishesControl.bind(this)} className="btn btn-primary ">Отправить</button>
                                        <p>Если у вас есть пожелания или предложения, то вы можете отправить их нам. Мы их объязательно рассмотрим в ближайшее время</p>
                                        
                                    </div> :
                                    <div>Настройка не найдена</div>   
                                }
                                </div>                            
                        </div>
                        : this.props.control.select === 4 ? <div className="cmd">
                                <ul className="item-menu">
                                    {
                                        control.cmd.map((item, key) => {
                                            return (
                                                <li key={key} className={this.props.control.focusItem === key ? "focus" : ""} onMouseOver={() => {this.props.setFocusItem(key)}} onClick={() => {this.props.setSelectItem(key)}} >{item}</li>
                                            )
                                        })
            
                                    }
                                </ul>
                                <div className="item-body scroll-block">
                                {
                                    this.props.control.selectItem === 0 ?
                                        <ul>
                                            <li><b>/id [name]</b> - поиск игрока по имени</li>
                                            <li><b>/showpass [id]</b> - показать паспорт</li>
                                            <li><b>/time</b> - узнать время</li>
                                            <li><b>/pay [id][money]</b> - передать деньги</li>
                                            <li><b>/ad [text]</b> - заказать объявление</li>
                                            <li><b>/admins</b> - Администрация Онлайн</li>
                                        </ul> :
                                    this.props.control.selectItem === 1 ?
                                        <ul>
                                            <li><b>/me [text]</b> - выполнить действие</li>
                                            <li><b>/do [text]</b> - событие игрового мира</li>
                                            <li><b>/try [text]</b> - попытаться выполнить действие</li>
                                            <li><b>/hi [id]</b> - пожать руку</li>
                                            <li><b>/w [text]</b> - прошепать</li>
                                            <li><b>/s [text]</b> - крикнуть</li>
                                        </ul> :
                                    this.props.control.selectItem === 2 ?
                                        <div>
                                            {
                                                this.props.control.profile.faction === 1 || this.props.faction === 6 ? 
                                                <ul>
                                                    <li><b>/arrest [id]</b> - арестовать игрока</li>
                                                    <li><b>/ticket [ID игрока] [сумма] [причина]</b> - выписать штраф</li>
                                                    <li><b>/su[ID игрока] [причина] [кол-во звезд]</b> - дать розыск </li>
                                                    <li><b>/clear [ID игрока]</b> - очистить уровень преступлений игрока </li>
                                                    <li><b>/wanted</b> - Посмотреть список розыска.</li>
                                                    <li><b>/cuff [ID игрока]</b> - надеть/снять наручники.</li>
                                                    <li><b>/frisk [ID игрока]</b> - обыскать игрока</li>
                                                    <li><b>/ram</b> - выбить дверь и проникнуть в дом </li>
                                                    <li><b>/find [id]</b> - узнать местоположение </li>
                                                    <li><b>/take [id]</b> - забрать лицензии, оружие и т.д</li>
                                                    <li><b>/warehouse</b> - узнать сколько БП на складе</li>
                                                    <li><b>/cput</b> - затащить в машину</li>
                                                    <li><b>/cout</b> - затащить в участок</li>
                                                    <li><b>/m</b> - СГУ </li>
                                                    <li><b>/members</b> - состав фракции</li>
                                                    <li><b>/d</b> - гос.департамент </li>
                                                    <li><b>/r</b> - рация</li>
                                                    <li><b>/givecopkeys</b> - выдать ключи от участка(для лидера)</li>
                                                    <li><b>/invite [id]</b> - пригласить игрока во фракцию(для лидера и зама)</li>
                                                    <li><b>/uninvite [id]</b> - уволить игрока из фракции(для лидера и зама)</li>
                                                    <li><b>/giverank [id] [+/-]</b> - повысить/понизить игрока своей фракции(для лидера и зама)</li>
                                                    <li><b>/repr [id] [reason]</b> - выдать выговор(для лидера и зама)</li>
                                                    <li><b>/gov</b> - волна гос. новостей(для лидера и зама)</li>
                                                </ul> :
                                                this.props.control.profile.faction === 2 ? 
                                                <ul>
                                                    <li><b>/warehouse</b> - узнать сколько БП на складе</li>
                                                    <li><b>/carm</b> - загрузиться/разгрузиться</li>
                                                    <li><b>/getgun</b> - взять оружие из машины</li>
                                                    <li><b>/d</b> - гос.департамент</li>
                                                    <li><b>/r</b> - рация</li>
                                                    <li><b>/members</b> - состав фракции</li>
                                                    <li><b>/armyar</b> - посадить в карцер(для лидера и зама)</li>
                                                    <li><b>/invite [id]</b> - пригласить игрока во фракцию(для лидера и зама)</li>
                                                    <li><b>/uninvite [id]</b> - уволить игрока из фракции(для лидера и зама)</li>
                                                    <li><b>/giverank [id] [+/-]</b> - повысить/понизить игрока своей фракции(для лидера и зама)</li>
                                                    <li><b>/repr [id] [reason]</b> - выдать выговор(для лидера и зама)</li>
                                                    <li><b>/gov</b> - волна гос. новостей(для лидера и зама)</li>
                                                </ul> :
                                                this.props.control.profile.faction === 3 ?
                                                <ul>
                                                    <li><b>/r</b> - рация</li>
                                                    <li><b>/heal [id]</b> -вылечить пациента</li>
                                                    <li><b>/members</b> - состав фракции</li>
                                                    <li><b>/lomka</b> - вылечить пациента от ломки</li>
                                                    <li><b>/narkozav</b> - вылечить пациента от наркозависимости</li>
                                                    <li><b>/seans</b> - провести сеанс от наркозависимости</li>
                                                    <li><b>/invite [id]</b> - пригласить игрока во фракцию(для лидера и зама)</li>
                                                    <li><b>/uninvite [id]</b> - уволить игрока из фракции(для лидера и зама)</li>
                                                    <li><b>/giverank [id] [+/-]</b> - повысить/понизить игрока своей фракции(для лидера и зама)</li>
                                                    <li><b>/repr [id] [reason]</b> - выдать выговор(для лидера и зама)</li>
                                                    <li><b>/gov</b> - волна гос. новостей(для лидера и зама)</li>
                                                </ul> :
                                                this.props.control.profile.faction === 4 ?
                                                <ul>
                                                    <li><b>/r</b> - рация</li>
                                                    <li><b>/d</b> - гос.департамент</li>
                                                    <li><b>/free [id] [price]</b> - освободить заключенных (Только для 2 ранга)</li>
                                                    <li><b>/subsidy [id]</b> - выдать суббсидию</li>
                                                    <li><b>/members</b> - состав фракции</li>
                                                    <li><b>/bank</b> - посмотреть казну</li>
                                                    <li><b>/rebank [price] </b> - положить на счет казны </li>
                                                    <li><b>/wibank [price] </b> - забрать деньги из казну (Только для лидера)</li>
                                                    <li><b>/demote [id] [ранг 0-уволить]</b> - повысить, понизить, уволить сотрудника ПД или Армии</li>
                                                    <li><b>/invite [id]</b> - пригласить игрока во фракцию(для лидера и зама)</li>
                                                    <li><b>/uninvite [id]</b> - уволить игрока из фракции(для лидера и зама)</li>
                                                    <li><b>/giverank [id] [+/-]</b> - повысить/понизить игрока своей фракции(для лидера и зама)</li>
                                                    <li><b>/repr [id] [reason]</b> - выдать выговор(для лидера и зама)</li>
                                                    <li><b>/gov</b> - волна гос. новостей(для лидера и зама)</li>                                                
                                                </ul> :
                                                this.props.control.profile.faction === 5 ?
                                                <ul>
                                                    <li><b>/r</b> - рация</li>
                                                    <li><b>/d</b> - гос.департамент</li>
                                                    <li><b>/members</b> - состав фракции</li>
                                                    <li><b>/ex [id] [sec]</b> - продлить срок заключения</li>
                                                    <li><b>/invite [id]</b> - пригласить игрока во фракцию(для лидера и зама)</li>
                                                    <li><b>/uninvite [id]</b> - уволить игрока из фракции(для лидера и зама)</li>
                                                    <li><b>/giverank [id] [+/-]</b> - повысить/понизить игрока своей фракции(для лидера и зама)</li>
                                                    <li><b>/repr [id] [reason]</b> - выдать выговор(для лидера и зама)</li>
                                                    <li><b>/gov</b> - волна гос. новостей(для лидера и зама)</li>
                                                </ul> :
                                                this.props.control.profile.faction === 1 || this.props.faction === 6 ? 
                                                <ul>
                                                    <li><b>/f</b> - чат фракции</li>
                                                    <li><b>/members</b> - состав фракции</li>
                                                    <li><b>/i</b> - говорить на языке мафии! </li>
                                                    <li><b>/tie</b> - связать/развязать (С 3 ранга)</li>
                                                    <li><b>/gag</b> - вставить/снять кляп (С 3 ранга)</li>
                                                    <li><b>/takephone</b> - забрать/отдать телефон (С 3 ранга)</li>
                                                    <li><b>/warehouse</b> - посмотреть кол-во материлов на складе мафии</li>
                                                    <li><b>/bizlist</b> - посмотреть список бизнесов,которые находятся под крышей мафии </li>
                                                    <li><b>/bizwar</b> - начать войну за бизнес (С 4 ранга)</li>
                                                    <li><b>/hackdb</b> - убрать себя или другого человека из розыска(С 4 ранга)</li>
                                                    <li><b>/getweapon</b> - взять оружие со склада (С 3 ранга)</li>
                                                    <li><b>/giveweapon</b> - передать оружие (С 3 ранга)</li>
                                                    <li><b>/bank</b> - посмотреть банк мафии</li>
                                                    <li><b>/rebank [price] </b> - положить на счет мафии </li>
                                                    <li><b>/wibank [price] </b> - забрать деньги из банка мафии (Только для лидера)</li>
                                                    <li><b>/invite [id]</b> - пригласить игрока во фракцию(для лидера и зама)</li>
                                                    <li><b>/uninvite [id]</b> - уволить игрока из фракции(для лидера и зама)</li>
                                                    <li><b>/giverank [id] [+/-]</b> - повысить/понизить игрока своей фракции(для лидера и зама)</li>
                                                    <li><b>/repr [id] [reason]</b> - выдать выговор(для лидера и зама)</li>
                                                </ul> :
                                                <div>Команды не найдены</div>
                                            }
                                        </div> :
                                    this.props.control.selectItem === 3 ?
                                        <div>
                                            {
                                                this.props.control.profile.job === 1 ?
                                                <ul>
                                                    <li><b>/repair [id] [price]</b> - отремонтировать ТС</li>
                                                    <li><b>/refill [id]</b> - заправить ТС</li>
                                                </ul> :
                                                this.props.control.profile.job === 2 ?
                                                <ul>
                                                    <li><b>/fare [price]</b> - выставить тариф</li>
                                                </ul> :
                                                this.props.control.profile.job === 3 ?
                                                <ul>
                                                    <li><b>/fare [price]</b> - выставить тариф</li>
                                                </ul> :
                                                <div>Команды не найдены</div>
                                            }
                                        </div> :
                                        <div>Команды не найдены</div>
                                }
                                </div>
                        </div>
                        : this.props.control.select === 5 ? <div className="rules">
                                <ul className="item-menu">
                                    {
                                        control.rules.map((item, key) => {
                                            return (
                                                <li key={key} className={this.props.control.focusItem === key ? "focus" : ""} onMouseOver={() => {this.props.setFocusItem(key)}} onClick={() => {this.props.setSelectItem(key)}} >{item}</li>
                                            )
                                        })
            
                                    }
                                </ul>
                                <div className="item-body scroll-block">
                                {
                                    this.props.control.selectItem === 0 ?
                                    <div>
                                        <h4>1.Общие положения</h4>
                                        <ul>
                                            <li>Правила сервера являются обязательными для всех игроков, независимо от пола, национальности и вероисповедания.</li>
                                            <li>Незнание или непонимание правил не освобождает от ответственности за их несоблюдение.</li>
                                            <li>Подключившись к серверу, Вы автоматически соглашайтесь с данными правилами.</li>
                                            <li>В силу необходимости контроля и изменения игровой структуры Администрация проекта вправе соответственно изменять правила серверов.</li>
                                        </ul>
                                        <h4>2.Игровой аккаунт</h4>
                                        <ul>
                                            <li>Ник игрока должен соответствовать формату<i> Имя_Фамилия.</i></li>
                                            <li>В целях безопасности пароль должен состоять минимум из 8 знаков, включающих цифры или буквы разного регистра.</li>
                                            <li>Ответственность за безопасность игрового аккаунта лежит, только на игроках.</li>
                                        </ul>
                                        <h4>3. Связь с администрацией на игровом сервере(report)</h4>
                                        <p>Порядок действий для связи с администрацией на сервере:</p>
                                        <ol>
                                            <li><i>С помощью клавиши &quot;Z&quot; открываем главное игровое меню.</i></li>
                                            <li><i>Далее переходим к репорту.</i></li>
                                            <li><i>Составляем соответсвующее обращение и отправляем.</i></li>
                                            <li><i>Ждем ответа. Все просто.</i></li>
                                        </ol>
                                        <ul>
                                            <li>Сообщение, отправленное в репорт, должно кратко и ясно излагать всю суть ситуации.</li>
                                            <li>При сообщении в репорт о нарушении правил другим игроком(ами), всегда стоит указывать его id и само нарушение.</li>
                                        </ul>
                                        <p>(К примеру. &quot;ID игрока - не отыгрывает RP-ситуацию&quot;,</p>
                                        <b>Запрещается отправка сообщений, не связанных с игровым сервером, не несущих смысловой нагрузки, имеющих нецензурную лексику, написанные заглавными буквами.</b>
                                        <h4>4. Игровой процесс</h4>
                                        <ul>
                                            <li>Запрещён взлом, извлечение, передача регистрационной информации игрока.</li>
                                            <li>Запрещена реклама различных сторонних ресурсов.</li>
                                            <li>Запрещены провокации и разжигания межнациональной розни, расистских, террористических, националистических идей.</li>
                                            <li>Запрещены любые оскорбления в сторону игрока.</li>
                                            <li>Запрещены nonRP &quot;разводы&quot;, продажа игрового имущества за реальные деньги.</li>
                                            <li>Запрещено использование запрещенного ПО, багов сервера, с целью получения игрового преимущества.</li>
                                            <li>Запрещено пребывание в состоянии AFK или принудительно завершать игровой сеанс для избежания смерти, арестов, похищения и создания помехи другим игрокам.</li>
                                            <li>Запрещено создавать проблемы другим игрокам, нарушая требования RP ситуации</li>
                                        </ul>
                                        <h4>5. Игровой чат.</h4>
                                        <ul>
                                            <li>общий игровой чат - чат,по которому игроки могу вести свободное общение между собой.&nbsp;</li>
                                            <li>Команда для использования</li>
                                            <ul>
                                                <li>/b - OOC информация</li>
                                                <li>/s - IC информация криком</li>
                                                <li>/w - IC информация шепотом</li>
                                                <li>T - стандартный чат IC информация</li>
                                            </ul>
                                            <li>чат департамента - чат, по которому передают/получают служебную информацию сотрудники государственных служб. Команда для использования &quot;/d&quot;</li>
                                            <li>чат государственных новостей - чат, по которому передают общую информацию особо важного характера для всех. Команда для использования &quot;/gov&quot;</li>
                                            <li>чат фракции - чат, по которому передают/получают информацию игроки во фракции. Команда для использования &quot;/f - банды/мафии/нелегальные организации; /r - государственные структуры; /rg - чат отдела государсвенной организации&quot;</li>
                                        </ul>
                                        <h5>В чате строго запрещено:</h5>
                                        <ul>
                                            <li>Флудить, спамить и засорять эфир разными способами.</li>
                                            <li>Написание сообщений заглавными буквами.</li>
                                            <li>Использовать OOC-информацию в IC-чате.</li>
                                            <li>Использовать скобки в IC-чате, для частичной передачи OOC-информации.</li>
                                        </ul>	
                                        <h4>6. Основные Role Play термины</h4>
                                        <ul>
                                            <li>Role-Play [RP] - это главный термин в игре, под которым подразумевается игра подобная действиям в реальной жизни.</li>
                                            <li>Non Role-Play [NonRP] - антоним термину RP, то есть поведение, не входящее в рамки RP. К нему относятся действия, которые бы персонаж никогда не сделал в реальной жизни.</li>
                                            <li>MеtaGаming [MG] - Использование OOC информации в IC чате и наоборот.</li>
                                            <li>PowerGaming [PG] - Заведомо ложное превышение своих физических возможностей в игре. (Пример: драка 1х3.)</li>
                                            <li>[DM] - Убийство без какой-либо IC причины.</li>
                                            <li>RevengeKill [RK] - Убийство-реванш персонажа, совершённое после того, как он уже убил Вас.</li>
                                            <li>SpawnKill [SK] - Убийство, совершённое на спавне,т.е. месте появление игроков.</li>
                                        </ul>
                                    </div> :
                                    this.props.control.selectItem === 2 ?
                                    <div>
                                        <b>1. Общие правила</b>
                                        <p>Обгон транспортного средства разрешен только с левой стороны, при этом водители обязаны убедиться, что встречная полоса свободна на достаточном для обгона расстояние. При ДТП водители обязаны позвонить в полицию, и дождаться ДПС</p>
                                        <b>2. Скорость движения</b>
                                        <p>В переделах города разрешается движение транспортных средств со скоростью не более 50 км/ч. В жилых зонах и на дворовых территориях не более 30 км/ч</p>
                                        <b>3. Остановка и стоянка</b>
                                        <p>Остановка и стоянка транспортных средств разрешаются на правой стороне дороги на обочине. В специальных отведённых для этого местах</p>
                                        <b>4. ДПС</b>
                                        <p>При виде автомобиля с включённой сереной, водитель обязан сбавить скорость и прижаться к обочине. Водитель обязан предъявить паспорт/лицензии, если тот попросил</p>
                                    </div> : <div>Правила не найдены</div>
                                }


                                </div>
                        </div>
                        : this.props.control.select === 6 ? <div className="default-text">{control.menu[this.props.control.select]}</div>
                        : <div></div>
                    }

                </div>
            )
        }    
    }      

export default connect(
  state => ({
    control: state.control
  }),
  dispatch => ({
    setFocus: (focus) => {
        dispatch({ type: 'CONTROL_FOCUS_MENU', focus});
    },
    setSelect: (select) => {
        dispatch({ type: 'CONTROL_SELECT_MENU', select});
    },
    setFocusItem: (focusItem) => {
        dispatch({ type: 'CONTROL_FOCUS_ITEM', focusItem});
    },
    setSelectItem: (selectItem) => {
        dispatch({ type: 'CONTROL_SELECT_ITEM', selectItem});
    }        
  })
)(Control);


