import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/dialog.css';

//import mp from'./mp.js';

import API from'./api.js';

window.setDialog = (title, input, buttonLeft, buttonRight , select, text) => {
    API.store.dispatch({ type: 'DIALOG_SET', title, input, buttonLeft, buttonRight, select, text:unescape(text)});
    window.setGui("dialog");
  }

class Dialog extends Component {
    constructor(props) {
        super(props)

        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleClick() {
        if(this.props.dialog.select < 0) return;
        if(this.props.dialog.select === 0 && this.props.dialog.buttonLeft === "") return;
        if(this.props.dialog.select === 1 && this.props.dialog.buttonRight === "") return;
        mp.trigger("select", this.props.dialog.select, this.props.dialog.input !== "" ? this.inputDialog.value : "");
        window.setGui(null);
    }

    handleKeyUp(event) {
	    if(event.keyCode === 13){
            event.preventDefault();
            this.handleClick();
        }
        else if(event.keyCode === 37 || event.keyCode ===  39) {
            event.preventDefault();
            if(event.keyCode === 37 && this.props.dialog.select === -1) this.props.selectDialog(2) 
            else if(event.keyCode === 39 && this.props.dialog.select === -1) this.props.selectDialog(1)
            else this.props.selectDialog(this.props.dialog.select === 1 ? 2 : 1)
        }
    }

    render() { 
        return (
            <div id="dialog">
                <div className="header">{this.props.dialog.title}</div>
                <div className="scroll-block body">
                    {
                        this.props.dialog.input !== "" ? <div><label dangerouslySetInnerHTML={{__html: this.props.dialog.input}}></label><input ref={(input) => { if(input) { this.inputDialog =  input; input.focus()}}} type="text" className="form-control" placeholder={this.props.dialog.input} /></div> : ""
                    }
                    <p dangerouslySetInnerHTML={{__html: this.props.dialog.text}}></p>
                </div>
                <div onMouseOver={() => {this.props.selectDialog(0)}} onClick={this.handleClick} className={this.props.dialog.select === 0 ? "active left" : "left"}>{this.props.dialog.buttonLeft}</div>
                <div onMouseOver={() => {this.props.selectDialog(1)}} onClick={this.handleClick} className={this.props.dialog.select === 1 ? "active right" : "right"}>{this.props.dialog.buttonRight}</div>
            </div>
        )
    }
}

export default connect(
    state => ({
        dialog: state.dialog
    }),
    dispatch => ({    
        selectDialog: (select) => {
            dispatch({ type: 'DIALOG_SELECT', select});
        },
    })
)(Dialog);


