var initial = {
    list: [],
    title: null,
    select: -1,
}

export default (state = initial, action) => {
    if (action.type === 'MENU_SET') {
        return {
             ...state,
            list: action.list,
            title: action.title,
            select: -1,
        }
    } else if(action.type === 'MENU_SELECT') {
        return {
             ...state,
            select: action.select,
        }
    }  
    return state;
}


