import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

import './css/phone.css';
import './css/glyphicons.min.css';

import signal from './img/phone/signal.png';

import API from'./api.js';

//import mp from'./mp.js';

window.setPhoneAlert = (text) => {
    let timer = setTimeout(() => { API.store.dispatch({ type: 'PHONE_ALERT', text: null , timer: 0}); }, 5000);
    API.store.dispatch({ type: 'PHONE_ALERT', text, timer});
}

window.addMessagePhone = (number,name,location,text) => {
    API.store.dispatch({ type: 'PHONE_MSG', number,name,location,text });
}


class Phone extends Component {
    constructor(props) {
        super(props)
        this.nameApps = {
            "phone": "Телефон",
            "signal": "Авиарежим",
            "message": "Сообщения",
            "balance": "Баланс",
            "info": "Справочник",
            "bank": "Банк",
            "gps" : "GPS",
            "911": "911",
            "store": "Магазин",
        }
        this.title911 = ["Полиция", "Скорая помощь", "Пожарные"]
        this.listInfo = ["Поиск номера", "Лидеры и замы", "Сервисы"]
        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.openApp = this.openApp.bind(this)
    } 
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
        if(event.keyCode === 13 && this.props.phone.openApp === null) {
            event.preventDefault();
            this.props.openApp(this.props.phone.apps[this.props.phone.focusApp]);
        } else if(event.keyCode === 37 && this.props.phone.openApp === null) {
            event.preventDefault();
            this.props.setFocusApp(API.leftSlider(this.props.phone.apps.length-1, this.props.phone.focusApp))
        } else if(event.keyCode === 39 && this.props.phone.openApp === null) {
            event.preventDefault();
            this.props.setFocusApp(API.rightSlider(this.props.phone.apps.length-1, this.props.phone.focusApp))
        
        } else if(event.keyCode === 27) {
            event.preventDefault();
            if(this.props.phone.windowApp !== null) this.props.openWindowApp(null) 
            else if(this.props.phone.openApp !== null) this.props.openApp(null) 
            else window.setGui(null)
        }
    }
    
    dialCall(number) {
        number = number.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(isNaN(parseInt(number, 10)) || parseInt(number, 10) > 999999) return window.setAlert("error", "Неправельно набран номер");
        mp.trigger("phoneCall", parseInt(number, 10));	        
    }
    openApp(key) {
        if(this.props.phone.apps[key] === "signal") mp.trigger("phoneSignal")
        else if(this.props.phone.apps[key] === "balance") mp.trigger("phoneBalance")
        else if(this.props.phone.apps[key] === "store" || 
                this.props.phone.apps[key] === "gps" || 
                this.props.phone.apps[key] === "bank") window.setPhoneAlert("Приложение в разработке!")
        else this.props.openApp(this.props.phone.apps[key])
    }


    render() {
        let alert;
        if (this.props.phone.alert) {
            alert = (
                <div key={this.props.phone.alert} className="phone-alert">{this.props.phone.alert}</div>
            )
        }
    
        return (
            <div id="phone">
                <div id="phone-bg" className=
                    {
                        this.props.phone.openApp === "phone" || this.props.phone.openApp === "message" ? "green" :
                        this.props.phone.openApp === "info" ? "orange" : "main"
                    }>
                    {
                        this.props.phone.signal ? <img className="phone-signal" alt={signal} src={signal} /> : <span className="phone-signal glyphicons glyphicons-plane"></span>
                    }
                    <span className="phone-time" >{new Date().getHours()}:{new Date().getMinutes()}</span>
                </div>
                <div id="phone-model"></div>
                <div id="phone-body">
                    <ReactCSSTransitionGroup   
                        transitionName="animation"
                        transitionEnter={true}
                        transitionEnterTimeout={800}
                        transitionLeave={true}
                        transitionLeaveTimeout={800}> 
                        {alert}       
                    </ReactCSSTransitionGroup>
                    {
                        this.props.phone.openApp === "message" ?
                            <div className="app-message">
                                <div className="app-message-header">
                                { 
                                    this.props.phone.windowApp === "new" ? "Новое сообщение" : !isNaN(parseInt(this.props.phone.windowApp, 10)) ? this.props.phone.historyMessage[this.props.phone.windowApp][0] : "Сообщения"
                                }
                                {
                                    this.props.phone.windowApp === null ? <span onClick={() => {this.props.openWindowApp("new")}} className="glyphicons glyphicons glyphicons-edit"></span> : <span onClick={() => {this.props.openWindowApp(null)}} className="glyphicons glyphicons-chevron-left"></span>
                                }
                                </div>         
                                {
                                    this.props.phone.windowApp === "new" ?
                                    <div className="app-message-new">
                                        <input ref={(input) => { if(input) { this.inputNumber =  input; }}} className="form-control-white" type="text" placeholder="кому" />
                                        <textarea className="form-control-white" placeholder="сообщение"></textarea>
                                        <button type="button" className="btn btn-success">Отправить сообщение</button>
                                    </div> :
                                    !isNaN(parseInt(this.props.phone.windowApp, 10)) ?
                                    <div className="app-message-history">
                                        <div className="app-message-history-body scroll-block black">
                                        {
                                            this.props.phone.historyMessage[this.props.phone.windowApp][1].map(([text, side],key) => {
                                                return (
                                                    <div key={key} className={side}>{text}</div>
                                                )
                                            })
                                        }
                                        </div>
                                        <div className="app-message-history-botton">
                                            <input ref={(input) => { if(input) { this.inputMessage =  input; }}} className="form-control-white" type="text" placeholder="сообщение" />
                                            <button type="button" className="btn btn-success">
                                                <span className="glyphicons glyphicons-chat"></span>
                                            </button>
                                        </div></div> :
                                    <div className="scroll-block black app-message-list">
                                        <ul>
                                            {
                                                Object.keys(this.props.phone.historyMessage).map((number) => {
                                                    let [name, messages] = this.props.phone.historyMessage[number]
                                                    return (
                                                        <li onClick={() => {this.props.openWindowApp(number)}} key={number} >{name}<p>{messages[messages.length-1][0]}</p></li>
                                                    )
                                                })
                                            }
                                        </ul>
                                    </div>
                                    }
                                </div> :
                        this.props.phone.openApp === "phone" ?
                            <div className="app-phone">
                                <div className="app-phone-header">
                                    { this.props.phone.windowApp === "recent" ? "Недавние" : "Вызов" }
                                </div>
                                {
                                    this.props.phone.windowApp === "recent" ? 
                                        <div className="app-phone-recent scroll-block black">
                                         <ul>
                                            {
                                                this.props.phone.historyCall.map(([number, name, type], key) => {
                                                    return (
                                                        <li key= {key} onClick={() =>{this.dialCall(number)}} >{name}<p>{type}</p></li>
                                                    )
                                                }) 
                                            }                                                
                                            </ul>
                                        </div> :
                                        <div className="app-phone-dial">
                                            <div className="dial-number">{this.props.phone.dialNumber}{ this.props.phone.dialNumber !== "" ?  <span onClick={this.props.dialDelete} className="glyphicons glyphicons-delete"></span> : <div></div> }</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(1)}}>1</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(2)}}>2</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(3)}}>3</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(4)}}>4</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(5)}}>5</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(6)}}>6</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(7)}}>7</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(8)}}>8</div>
                                            <div className="number" onClick={() => {this.props.dialNumber(9)}}>9</div>
                                            <div className="clean" onClick={this.props.dialClean}>
                                                <span className="glyphicons glyphicons-remove-circle"></span>
                                            </div>
                                            <div className="number" onClick={() => {this.props.dialNumber(0)}}>0</div>
                                            <div className="call" onClick={() =>{this.dialCall(this.props.phone.dialNumber)}}>
                                                <span className="glyphicons glyphicons-earphone"></span>
                                            </div>
                                        </div>
                                }  
                                <div className= "app-phone-botton">
                                    <div className="recent" onClick={() => {this.props.openWindowApp("recent")}}>Недавние</div>
                                    <div className="dial" onClick={() => {this.props.openWindowApp(null)}}>Вызов</div>
                                </div>
                            </div> :
                        this.props.phone.openApp === "911" ?
                            <div className="app-911">
                            {
                                    !isNaN(parseInt(this.props.phone.windowApp, 10)) ? 
                                    <div>
                                        <div className="header">{this.title911[parseInt(this.props.phone.windowApp, 10)]}</div>
                                        <input className="form-control-white" type="text" ref={(input) => { if(input) { this.inputMessage =  input; }}} />
                                        <span onClick={() => {this.props.openWindowApp(null)}} className="glyphicons glyphicons-unshare"></span>
                                        <span onClick={() => {this.props.openWindowApp(null)}} className="glyphicons glyphicons-edit"></span>
                                    </div> :
                                    <div>
                                        <div className="header">Экстренный вызов</div>
                                        <ul>
                                        {
                                            this.title911.map((title, key) => {
                                                return (
                                                    <li key={key} onClick={() => {this.props.openWindowApp(key)}}>{title}</li>
                                                )
                                            })
                                        }
                                        </ul>      
                                    </div>
                                }                     
                            </div> :
                        this.props.phone.openApp === "info" ?
                            <div className="app-info">
                                <div className="app-info-header">
                                Справочник
                                </div> 
                                <div className="app-info-body scroll-block black">
                                    {
                                        !isNaN(parseInt(this.props.phone.windowApp, 10)) ? 
                                        <div>
                                        </div> :
                                        <ul>
                                        {
                                            this.listInfo.map((item, key) => {
                                                return (
                                                    <li key={key} onClick={() => {this.props.openWindowApp(key)}}>{item}</li>        
                                                )
                                            })
                                        }
                                        </ul>
                                    }
                                </div>
                            </div>
                        :   <div className="app-home">
                                <div className="app-home-body">
                                    {
                                        this.props.phone.apps.map((item, key) => {
                                            return (
                                                <div key={key} className="phone-app" onMouseOver={() => {this.props.setFocusApp(key)}} onClick={() => {this.openApp(key)}}>
                                                    <div className={this.props.phone.focusApp === key ? "focus" : ""}></div>
                                                    <img src={require(`./img/phone/apps/${item}.png`)} alt={item} />
                                                    <p>{this.nameApps[item]}</p>
                                                </div>
                                            )
                                        })
                                    } 
                                </div>
                            </div>
                    }
                </div>
                <div id="phone-home" onClick={() => {this.props.openApp(null)}}></div>
            </div>
        )
    }
}

export default connect(
    state => ({
        phone: state.phone
    }),
    dispatch => ({
        openWindowApp: (window) => {
            dispatch({ type: 'PHONE_OPEN_WINDOW_APP', window });
        },   
        dialNumber: (number) => {
            dispatch({ type: 'PHONE_DIAL_NUMBER', number });
        }, 
        dialClean: () => {
            dispatch({ type: 'PHONE_DIAL_CLEAN'});
        }, 
        dialDelete: () => {
            dispatch({ type: 'PHONE_DIAL_DELETE'});
        }, 
        setFocusApp: (focus) => {
            dispatch({ type: 'PHONE_FOCUS_APP', focus });
        },        
        openApp: (open) => {
            dispatch({ type: 'PHONE_OPEN_APP', open });
        },   
        setMessage: (message) => {
            dispatch({ type: 'PHONE_MESSAGE', message });
        },
    })
)(Phone);


