var initial = {
    id: 0,
    list: {}
}
export default (state = initial, action) => {
    if (action.type === 'ALERT_ADD_TEXT') {
        let list = state.list
        list[state.id] = [action.typeAlert, action.text];
        return {
             ...state,
             list: list,
             id: state.id+1

        }
    } else if(action.type === 'ALERT_REMOVE') {
        let list = state.list
        for(let id in list){
            delete list[id]
            break;
        }
        return {
            ...state,
            list: list,

       }
    }
    return state;
}


