import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/speedometer.css';

import API from './api';

window.setSpeed = (number) => {
    let speed = number < 0 ? 0 : number > 330 ? 330 : number;
    API.store.dispatch({ type: 'SPEEDOMETER_SPEED_SET', speed});
  }
  
  window.setFuel = (number) => {
    let fuel = number < 0 ? 0 : number > 80 ? 80 : Math.round(number);
    API.store.dispatch({ type: 'SPEEDOMETER_FUEL_SET', fuel});
  }
  
  window.setEngine = (engine) => {
    API.store.dispatch({ type: 'SPEEDOMETER_ENGINE_SET', engine});
  }
  
  window.setLockCar = (lock) => {
    API.store.dispatch({ type: 'SPEEDOMETER_LOCK_SET', lock});
  }

class Speedometer extends Component {

    constructor(props) {
        super(props)
        this.dataFuel = {
            1:[-78.5, 10, 1.2],
            2:[-64.5, 20, 1.35],
            3:[-51, 30, 1.35],
            4:[-37.5, 40, 1.35],
            5:[-24, 50, 1.35],
            6:[-10.5, 60, 1.35],
            7:[3, 70, 1.35],
            8:[15, 80, 1.2],
        }       
        this.dataSpeed = {
            1:[-101, 10, 1],
            2:[-90, 20, 1.1],
            3:[-79, 30, 1.15],
            4:[-67.50, 40, 1.15],
            5:[-56, 50, 1.1],
            6:[-45, 60, 1.1],
            7:[-34, 70, 1.15],
            8:[-22.5, 80, 1.15],
            9:[-22.5, 80, 0.46],
            10:[-22.5, 80, 0.46],
            11:[-11, 105, 0.44],
            12:[-11, 105, 0.44],
            13:[0, 130, 0.44],
            14:[0, 130, 0.44],
            15:[0, 130, 0.44],
            16:[11, 155, 0.46],
            17:[11, 155, 0.46],
            18:[22.5, 180, 0.44],
            19:[22.5, 180, 0.44],
            20:[22.5, 180, 0.44],
            21:[33.5, 205, 0.46],
            22:[33.5, 205, 0.46],
            23:[45, 230, 0.44],
            24:[45, 230, 0.44],
            25:[45, 230, 0.44],
            26:[56, 255, 0.46],
            27:[56, 255, 0.46],
            28:[67.5, 280, 0.46],
            29:[67.5, 280, 0.46],
            30:[67.5, 280, 0.46],
            31:[79, 305, 0.4],
            32:[79, 305, 0.4],
            33:[79, 305, 0.4]
        }    
    }

    render() {
        let [d1,d2,d3] = this.props.speedometer.fuel === 0 ? this.dataFuel[1] : this.dataFuel[Math.ceil(this.props.speedometer.fuel/10)]
        let [m1,m2,m3] = this.props.speedometer.speed === 0 ? this.dataSpeed[1] : this.dataSpeed[Math.ceil(this.props.speedometer.speed/10)]
         return (
            <div id="speedometer">
                <div className="fuel-shadow">
                    <div className="fuel">
                        <div className="fuel-logo"></div>
                        <div className="fuel-cursor" style={{transform: "rotate(" + String(d1-(d2-this.props.speedometer.fuel)*d3) + "deg)"}}></div>
                    </div>
                </div>
                <div className="speed-shadow">
                    <div className="speed">
                        <div className={this.props.speedometer.engine ? "engine-logo on" : "engine-logo off" }></div>
                        <div className={!this.props.speedometer.lock ? "lock-logo on" : "lock-logo off" }></div>
                        <div className="speed-cursor" style={{transform: "rotate(" + String(m1-(m2-this.props.speedometer.speed)*m3) + "deg)"}}></div>
                    </div>
                </div>     
            </div> 
        )
    }
}

export default connect(
    state => ({
        speedometer: state.speedometer
    }),
    dispatch => ({
        
    })
)(Speedometer);


