import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/auth.css';
import './css/glyphicons.min.css';

import logo from './img/logo.png'; // логотип при входе

//import mp from'./mp.js';

import API from'./api.js';

window.login = (binds) => {
    API.store.dispatch({ type: 'AUTH_LOGGED', logged: true});
    API.store.dispatch({ type: 'BINDER_SET', binds: JSON.parse(unescape(binds))});
  }
  
  window.setLogin = (name, password) => {
    API.store.dispatch({ type: 'AUTH_UPDATE_NAME', name });
    API.store.dispatch({ type: 'AUTH_UPDATE_PASSWORD', password });
  }

class Auth extends Component {

    constructor(props) {
        super(props)
        
        this.handleKeyUp = this.handleKeyUp.bind(this)
    }

    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {  
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
        if(event.keyCode === 13) {
            event.preventDefault();
            this.handleClick();
        }     
    }
    handleClick(){
        if(this.props.gui === "login") {
            let name = this.props.auth.name.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
            let password = this.props.auth.password.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
            if(name.length < 3 || name.length > 30) return window.setAlert("error", "Длина игрового имени должна быть не меньше 3 и не больше 32 символов");
            if(!/^([A-Z][a-z]+_[A-Z][a-z]+)$/.test(name))  return window.setAlert("error", "Некорректное игровое имя. Пример РП никнейма: John_Dow");
            if(password.length < 6) return window.setAlert("error", "Длина пароль должна быть не меньше 6 символов");
            mp.trigger("loginAuth", name, password);
            window.setGui(null);
        } else if(this.props.gui === "reg") {
            let name = this.props.auth.name.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
            let password = this.props.auth.password.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
            let email = this.props.auth.email.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
            if(name.length < 3 || name.length > 30) return window.setAlert("error", "Длина игрового имени должна быть не меньше 3 и не больше 32 символов");
            if(!/^([A-Z][a-z]+_[A-Z][a-z]+)$/.test(name))  return window.setAlert("error", "Некорректное игровое имя. Пример РП никнейма: John_Dow");
            if(password.length < 6) return window.setAlert("error", "Длина пароль должна быть не меньше 6 символов");
            if(email !== "" && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) return window.setAlert("error", "Некорректный адрес электронной почты. Пример: admin@unionrp.info");
            window.setGui("personage");
            mp.trigger("CheckThatName", name);
        }
    }

    render() { 
        return (
            this.props.gui === "login" ?    <div id="login" className="auth">
                <div className="auth-img"><img src={logo} alt="logo" /></div>
                <div className="auth-body">
                    <div className="auth-header">Авторизация</div>
                    <div className="auth-form">
                        <div className={this.props.auth.inputFocus === "name" ? "input-auth focus" : this.props.auth.name !== "" ? "select input-auth" : "input-auth"} >
                            <span className="glyphicons glyphicons-user"></span>
                            <input onChange={(e) => {this.props.updateName(e.target.value)}} value={this.props.auth.name} onFocus={()=>{ this.props.inputFocus("name") }} onBlur={()=>{ this.props.inputFocus(null); }}  type="text"/>
                            <label>Игровое имя</label>
                        </div>                    
                        <div className={this.props.auth.inputFocus === "password" ? "input-auth focus" : this.props.auth.password !== "" ? "select input-auth" : "input-auth"} >
                            <span className="glyphicons glyphicons-lock"></span>
                            <input onChange={(e) => {this.props.updatePassword(e.target.value)}} value={this.props.auth.password} onFocus={()=>{ this.props.inputFocus("password") }} onBlur={()=>{ this.props.inputFocus(null) }}  type="password"/>
                            <label>Пароль</label>
                        </div>
                        <button type="button"  onClick={this.handleClick.bind(this)} className="btn btn-primary btn-lg">Войти</button>
                        <button type="button"  onClick={() => {window.setGui("reg")}}  className="btn btn-primary btn-lg">Регистрация</button>
                    </div>
                </div>
            </div>   
             : this.props.gui === "reg" ?   <div id="reg" className="auth">
            <div className="auth-img"><img src={logo} alt="logo" /></div>
            <div className="auth-body">
                <div className="auth-header">Регистрация</div>
                <div className="auth-form">
                    <div className={this.props.auth.inputFocus === "name" ? "input-auth focus" : this.props.auth.name !== "" ? "select input-auth" : "input-auth"} >
                        <span className="glyphicons glyphicons-user"></span>
                        <input onChange={(e) => {this.props.updateName(e.target.value)}} value={this.props.auth.name} onFocus={()=>{ this.props.inputFocus("name") }} onBlur={()=>{ this.props.inputFocus(null); }}  type="text"/>
                        <label>Игровое имя</label>
                    </div> 
                    <div className={this.props.auth.inputFocus === "email" ? "input-auth focus" : this.props.auth.email !== "" ? "select input-auth" : "input-auth"} >
                        <span className="glyphicons glyphicons-envelope"></span>
                        <input onChange={(e) => {this.props.updateEmail(e.target.value)}} value={this.props.auth.email} onFocus={()=>{ this.props.inputFocus("email") }} onBlur={()=>{ this.props.inputFocus(null) }}  type="email"/>
                        <label>Электронная почта</label>
                    </div> 
                    <div className={this.props.auth.inputFocus === "password" ? "input-auth focus" : this.props.auth.password !== "" ? "select input-auth" : "input-auth"} >
                        <span className="glyphicons glyphicons-lock"></span>
                        <input onChange={(e) => {this.props.updatePassword(e.target.value)}} value={this.props.auth.password} onFocus={()=>{ this.props.inputFocus("password") }} onBlur={()=>{ this.props.inputFocus(null) }}  type="password"/>
                        <label>Пароль</label>
                    </div>                                 
                    <button type="button"  onClick={this.handleClick.bind(this)} className="btn btn-primary btn-lg">Продолжить</button>
                    <button type="button"  onClick={() => {window.setGui("login")}} className="btn btn-primary btn-lg">Назад</button>
                </div>
            </div>
        </div>
        : <div></div>
        )
    }
}

export default connect(
    state => ({
        auth: state.auth,
        gui: state.gui.open
    }),
    dispatch => ({
        inputFocus: (focus) => {
            dispatch({ type: 'AUTH_INPUT_FOCUS', focus});
        },
        authReg: (password, email) => {
            dispatch({ type: 'AUTH_REG', email, password });
        },         
        updatePassword: (password) => {
            dispatch({ type: 'AUTH_UPDATE_PASSWORD', password });
        },
        updateName: (name) => {
            dispatch({ type: 'AUTH_UPDATE_NAME', name });
        },
        updateEmail: (email) => {
            dispatch({ type: 'AUTH_UPDATE_EMAIL', email });
        },
    })
)(Auth);


