var initial = {
    cmd: []
}

export default (state = initial, action) => {
    if (action.type === 'CONSOLE_CMD') {
        let cmd = state.cmd;
        cmd.push(action.cmd);
        return {
             ...state,
             cmd: cmd,
        }           
    }
    return state;
}


