import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/shop.css';
import './css/glyphicons.min.css';

import API from'./api.js';
import { shop } from'./config.js';

//import mp from'./mp.js';

class Shop extends Component {
    constructor(props) {
        super(props)


        window.messageShop = this.props.setMessage

        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.handleClick = this.handleClick.bind(this)
    } 
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
        console.log(event)
        if(event.keyCode === 13) {
            event.preventDefault();
            this.handleClick();
        } else if(event.keyCode === 38) {
            event.preventDefault();
            this.props.setActive(API.leftSlider(shop.shopList.length-1, this.props.shop.active))
        } else if(event.keyCode === 40) {
            event.preventDefault();
            this.props.setActive(API.rightSlider(shop.shopList.length-1, this.props.shop.active))
        } else if(event.keyCode === 37) {
            event.preventDefault();
            this.props.setFocus(API.leftSlider(shop.itemList[this.props.shop.active].length-1, this.props.shop.focus))
        } else if(event.keyCode === 39) {
            event.preventDefault();
            this.props.setFocus(API.rightSlider(shop.itemList[this.props.shop.active].length-1, this.props.shop.focus))
        } else if(event.keyCode === 27) {
            event.preventDefault();
            window.setGui(null)
        }
    }

    handleClick(){
        if(this.props.shop.focus === -1) return;
        mp.trigger("buyShop", shop.itemList[this.props.shop.active][this.props.shop.focus][0])
    }

    render() {

        return (
            <div id="shop">
                <div className="shop-header">Магазин</div>
                <div className="shop-close" onClick={() => { window.setGui(null)} }><span className="glyphicons glyphicons-remove"></span></div>
                <ul className="shop-menu">
                    {
                        shop.shopList.map(([name, icon], key) => {
                            return (
                                <li  key={key} className={this.props.shop.active === key ? "active" : "" } onMouseOver={() => {this.props.setActive(key)}}>
                                    <span className={icon}></span><br />{name}
                                </li>
                            )
                        })
                    }
                </ul>
                <div className="scroll-block shop-body">
                {
                    shop.itemList[this.props.shop.active].map(([name, title, price], key) => {
                        return (
                            <div key={key} onClick={this.handleClick} className={this.props.shop.focus === key ? "item focus" : this.props.shop.select === key ? "select item" : "item" } onMouseOver={() => {this.props.setFocus(key)}}>
                                {
                                    this.props.shop.message && this.props.shop.focus === key ?
                                        <div className="message">{this.props.shop.message}<br/>Приятного аппетита!</div>:
                                        <div>
                                            <img alt={name} src={require(`./img/shop/${name}.png`)} />
                                            <div className="description">
                                                {title}<br /><span className="glyphicons glyphicons-shopping-cart"></span> ${price}
                                            </div> 
                                        </div>
                                }
                            </div>
                        )
                    })
                }
                </div>
            </div>
        )
    }
}

export default connect(
    state => ({
        shop: state.shop
    }),
    dispatch => ({
        setActive: (active) => {
            dispatch({ type: 'SHOP_ACTIVE', active });
        },        
        setFocus: (focus) => {
            dispatch({ type: 'SHOP_FOCUS', focus });
        },   
        setMessage: (message) => {
            dispatch({ type: 'SHOP_MESSAGE', message });
        },           
    })
)(Shop);


