import React, { Component } from 'react';
import { connect } from 'react-redux';

import Chat from './chat';
import Dialog from './dialog';
import Menu from './menu';
import Control from './control';
import Auth from './auth';
import Alert from './alert';
import Personage from './personage';
import Binder from './binder';
import Atm from './atm';
import Shop from './shop';
import Phone from './phone';
import Speedometer from './speedometer';
//import Console from './console';
import Interaction from './interaction';
import Police from './police';

//import mp from'./mp.js';

class Gui extends Component {
    constructor(props) {
        super(props)

        window.setGui = this.props.setGui
        window.setSpeedometer = this.props.setSpeedometer
    } 

    componentWillUpdate(nextProps, nextState) {
        if(this.props.gui.open !== nextProps.gui.open) {
            if(nextProps.gui.open === null  && nextProps.gui.cursor === true) {
                this.props.setCursor(false)
            } else if(( nextProps.gui.open === "dialog" || 
                        nextProps.gui.open === "menu" || 
                        nextProps.gui.open === "control" || 
                        nextProps.gui.open === "phone" || 
                        nextProps.gui.open === "shop" || 
                        nextProps.gui.open === "reg" || 
                        nextProps.gui.open === "binder" || 
                        nextProps.gui.open === "login" ||
                      //  nextProps.gui.open === "console" ||
                        nextProps.gui.open === "interaction" ||  
                        nextProps.gui.open === "police" ||  
                        nextProps.gui.open === "atm") 
                && nextProps.gui.cursor === false) {
                    this.props.setCursor(true)
                }
        }
    }

    render() {
        if(this.props.chat && this.props.gui.open) window.chatAPI.activate(false)
        return (
            <div>
                <Alert /><Chat /><Binder /><Interaction />
                {
                    this.props.gui.speed === true ? <Speedometer /> : <div></div>
                }
                {
                    (this.props.gui.open === "dialog") ? <Dialog /> :
                    (this.props.gui.open === "menu") ? <Menu /> :
                    (this.props.gui.open === "control") ? <Control /> :
                    (this.props.gui.open === "phone") ? <Phone /> :
                    (this.props.gui.open === "shop") ? <Shop /> :
                    (this.props.gui.open === "atm") ? <Atm /> :
                    (this.props.gui.open === "personage") ? <Personage /> :
                 //   (this.props.gui.open === "console") ? <Console /> :
                    (this.props.gui.open === "police") ? <Police /> :
                    (this.props.gui.open === "reg" || this.props.gui.open === "login") ? <Auth /> : <div></div>
        
                }
            </div>
        )
    }
}

export default connect(
    state => ({
        gui: state.gui,
        chat: state.chat.active,
        logged: state.auth.logged
    }),
    dispatch => ({
        setSpeedometer: (speed) => {
            dispatch({type: 'GUI_SET_SPEED', speed});
        },
        setGui: (gui) => {
            dispatch({type: 'GUI_SET', gui});
        },
        setCursor: (cursor) => {
            dispatch({type: 'GUI_CURSOR', cursor});
            mp.invoke("focus", cursor)
        }        
    })
)(Gui);


