var initial = {
    active: 1,
    message: null,
    focus: -1,
}

export default (state = initial, action) => {
    if (action.type === 'SHOP_FOCUS') {
        return {
             ...state,
            focus: action.focus,
            message: null
        }           
    }  else if (action.type === 'SHOP_MESSAGE') {
        return {
             ...state,
            message: action.message,
        }           
    }  else if (action.type === 'SHOP_ACTIVE') {
        return {
             ...state,
            active: action.active,
            focus: -1,
            message: null
        }           
    }  
    return state;
}


