import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/police.css';
import './css/glyphicons.min.css';

import { police } from'./config.js';
import API from'./api.js';

//import mp from'./mp.js';

window.setPoliceNames = (list) => {
    API.store.dispatch({ type: 'POLICE_SET_LIST_NAME', list : JSON.parse(unescape(list))});
  }
  
  window.setPoliceCall = (list) => {
    API.store.dispatch({ type: 'POLICE_SET_LIST_CALL', list : JSON.parse(unescape(list))});
  }
  
  window.setPoliceMembers = (list, leader) => {
    API.store.dispatch({ type: 'POLICE_SET_MEMBER', leader, list : JSON.parse(unescape(list))});
  }
  
  window.setPoliceInfo = (name, star, fine) => {
    API.store.dispatch({ type: 'POLICE_SET_INFO', name, star, fine : JSON.parse(unescape(fine))});
  }
  
  window.setPoliceWanted = (list) => {
    API.store.dispatch({ type: 'POLICE_SET_WANTED', list : JSON.parse(unescape(list))});
  }
  
  window.setPoliceStat = (list) => {
    API.store.dispatch({ type: 'POLICE_SET_STATS', list : JSON.parse(unescape(list))});
  }
  
  window.updatePoliceStatus = (name, status) => {
    API.store.dispatch({ type: 'POLICE_UPDATE_STATUS', name, status});
  }

class Police extends Component {
    constructor(props) {
        super(props)

        this.handleKeyUp = this.handleKeyUp.bind(this)
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
        this.props.setUpdateStatus("name", true)
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
	    if(event.keyCode === 13){
            event.preventDefault();
            if(this.props.police.select === 0) this.sendSearch(); 
            else if(this.props.police.select === 1) this.sendFine();
            else if(this.props.police.select === 2 && this.props.police.info.name === "") this.getInfo();
        } else if(event.keyCode === 27) {
            event.preventDefault();
            window.setGui(null)
        }
    }
    
    handleInputChange(e) {
        this.props.setName(e.target.value);
        let selectName = []
        this.props.police.name.list.forEach(name => {
            if(name.toLowerCase().indexOf(e.target.value.toLowerCase()) >= 0) 
            selectName.push(name);
        });
        this.props.setSelectName(selectName);
    }  

    sendSearch() {
        let name = this.props.police.name.select.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        let id = this.props.police.name.list.indexOf(name);
        if(id === -1) return window.setAlert("error", "Игрок не найден");
        let reason = this.inputReason.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(reason.length === 0) return window.setAlert("error", "Укажите причину");
        if(reason.length > 50) return window.setAlert("error", "Длина причины должна не превышать 50 символов");
        mp.trigger("policeSu", id, this.props.police.star, reason);
        this.props.setName("");
        this.props.setSelectName(this.props.police.name.list);
        this.inputReason.value = "";
        
    }

    sendFine() {
        let name = this.props.police.name.select.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        let id = this.props.police.name.list.indexOf(name);
        if(id === -1) return window.setAlert("error", "Игрок не найден");
        let price = this.inputPrice.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(price.length === 0) return window.setAlert("error", "Укажите сумму штрафа");
        if(isNaN(parseInt(price, 10)) || price < 100 || price > 10000) return window.setAlert("error", "Укажите число от $100 до $10000");
        let reason = this.inputReason.value.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        if(reason.length === 0) return window.setAlert("error", "Укажите причину");
        if(reason.length > 50) return window.setAlert("error", "Длина причины должна не превышать 50 символов");
        mp.trigger("policeFine", id, price, reason);
        this.props.setName("");
        this.props.setSelectName(this.props.police.name.list);
        this.inputReason.value = "";
        this.inputPrice.value = "";
    }

    getInfo() {
        let name = this.props.police.name.select.replace( /"/g, "'" ).replace(/^\s\s*/, '').replace(/\s\s*$/, '');
        let id = this.props.police.name.list.indexOf(name);
        if(id === -1) return window.setAlert("error", "Игрок не найден");
        mp.trigger("policeInfo", id);
        this.props.setName("");       
    }

    render() { 
        return (
            <div id="police">
                <div className="left">
                    <div className="header">Навигация</div>
                    <ul>
                    {
                        police.menu.map((item, key) => {
                            return (
                                <li key = {key} 
                                    className={this.props.police.select === key ? "active" : ""}  
                                    onClick={() => {this.props.setSelect(key)}} 
                                    dangerouslySetInnerHTML={{__html: item}}
                                ></li> 
                            )
                        }) 
                    }
                    </ul>
                </div>
                <div className="right"><div className="header">
                    {
                        police.menu[this.props.police.select]
                    }
                    {
                        this.props.police.select === 2 && this.props.police.info.name !== "" ? <span onClick={() => {this.props.setInfo("", 0, [])}}  className="glyphicons glyphicons-chevron-left"></span> :
                        this.props.police.select === 3 ? <span onClick={() => {if(this.props.police.update.call === false) this.props.setUpdateStatus("call", true)}}  className="glyphicons glyphicons-refresh" ></span> :
                        this.props.police.select === 4 ? <span onClick={() => {if(this.props.police.update.wanted === false) this.props.setUpdateStatus("wanted", true)}}  className="glyphicons glyphicons-refresh" ></span> :
                        this.props.police.select === 5 ? <span onClick={() => {if(this.props.police.update.member === false) this.props.setUpdateStatus("member", true)}}  className="glyphicons glyphicons-refresh" ></span> :
                        this.props.police.select === 6 ? <span onClick={() => {if(this.props.police.update.stats === false) this.props.setUpdateStatus("stats", true)}}  className="glyphicons glyphicons-refresh" ></span> : 
                        <div></div>
                    }
                    <span  onClick={() => {window.setGui(null)}} className="glyphicons glyphicons-remove"></span>
                </div>
                    {
                        this.props.police.select === 0 ? <div id="search" >
                            <div className="nameList">
                                <b>Cписок игроков</b><span onClick={() => {if(this.props.police.update.name === false) this.props.setUpdateStatus("name", true)}}  className="glyphicons glyphicons-refresh" ></span>
                            {
                                this.props.police.update.name === false ?  <ul className=" scroll-block">
                                    {
                                        this.props.police.name.filter.map((name, key) => {
                                            return (
                                                <li key = {key} 
                                                    onClick={() => {this.props.setName(name)}} 
                                                    dangerouslySetInnerHTML={{__html: name}}
                                                ></li> 
                                            )
                                        }) 
                                    }
                                    </ul> : <div className="alert">Загрузка..</div>
                            }

                            </div>
                            <div className="label">Имя игрока: </div>
                            <input className="form-control-white" type="text" onChange = {this.handleInputChange.bind(this)} value = {this.props.police.name.select}/><br/><br/>
                            <div className="star">
                                <b>Уровень розыска: </b>
                                <span onClick={()=>{this.props.setStar(1)}} className= {this.props.police.star >= 1 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                <span onClick={()=>{this.props.setStar(2)}} className= {this.props.police.star >= 2 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                <span onClick={()=>{this.props.setStar(3)}} className= {this.props.police.star >= 3 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                <span onClick={()=>{this.props.setStar(4)}} className= {this.props.police.star >= 4 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                <span onClick={()=>{this.props.setStar(5)}} className= {this.props.police.star >= 5 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                            </div><br/>
                            <div className="label">Причина:  </div>
                            <input ref={(input) => { if(input) { this.inputReason =  input; }}} className="form-control-white" type="text"/><br/><br/><br/>
                            <span className="send" onClick={this.sendSearch.bind(this)}>Отправить</span>
                        </div> : this.props.police.select === 1 ? <div id="fine" >
                            <div className="nameList">
                                <b>Cписок игроков</b><span onClick={() => {if(this.props.police.update.name === false) this.props.setUpdateStatus("name", true)}}  className="glyphicons glyphicons-refresh" ></span>
                            {
                                this.props.police.update.name === false ?  <ul className=" scroll-block">
                                    {
                                        this.props.police.name.filter.map((name, key) => {
                                            return (
                                                <li key = {key} 
                                                    onClick={() => {this.props.setName(name)}} 
                                                    dangerouslySetInnerHTML={{__html: name}}
                                                ></li> 
                                            )
                                        }) 
                                    }
                                    </ul> : <div className="alert">Загрузка..</div>
                            }
                            </div>
                            <div className="label">Имя игрока: </div>
                            <input className="form-control-white" type="text" onChange = {this.handleInputChange.bind(this)} value = {this.props.police.name.select}/><br/><br/>
                            <div className="label">Сумма: </div>
                            <input ref={(input) => { if(input) { this.inputPrice =  input; }}} className="form-control-white" type="text"/><br/><br/>              
                            <div className="label">Причина: </div>
                            <input ref={(input) => { if(input) { this.inputReason =  input; }}} className="form-control-white" type="text"/><br/><br/><br/>
                            <span className="send"  onClick={this.sendFine.bind(this)}>Отправить</span>
                            </div> : this.props.police.select === 2 ? <div id="info" className=" scroll-block">
                            {
                                this.props.police.info.name === "" ? <div>
                                    <div className="nameList">
                                        <b>Cписок игроков</b><span onClick={() => {if(this.props.police.update.name === false) this.props.setUpdateStatus("name", true)}}  className="glyphicons glyphicons-refresh" ></span>
                                        {
                                            this.props.police.update.name === false ?  <ul className=" scroll-block">
                                                {
                                                    this.props.police.name.filter.map((name, key) => {
                                                        return (
                                                            <li key = {key} 
                                                                onClick={() => {this.props.setName(name)}} 
                                                                dangerouslySetInnerHTML={{__html: name}}
                                                            ></li> 
                                                        )
                                                    }) 
                                                }
                                                </ul> : <div className="alert">Загрузка..</div>
                                        }
                                    </div>
                                    <div className="label">Имя игрока: </div>
                                    <input className="form-control-white" type="text" onChange = {this.handleInputChange.bind(this)} value = {this.props.police.name.select}/><br/><br/><br/>
                                    <span className="send" onClick={this.getInfo.bind(this)} >Отправить</span>
                                </div> : <div>
                                    <div className="title">Имя игрока:</div>
                                    <div className="name">{this.props.police.info.name}</div>
                                    <div className="title">Розыск:</div>
                                    <div className="star">
                                        <span className= {this.props.police.info.star >= 1 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                        <span className= {this.props.police.info.star >= 2 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                        <span className= {this.props.police.info.star >= 3 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                        <span className= {this.props.police.info.star >= 4 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                        <span className= {this.props.police.info.star >= 5 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                    </div><div className="header-fine" >Список штрафов</div>
                                    {
                                        this.props.police.info.fine.length === 0 ? <div className="alert">Штрафов нет</div> :
                                        this.props.police.info.fine.map(([date, price, reason], key) => {
                                            return (
                                                <div key={key}>
                                                    <div className="date">{API.formatDate(date)}</div>
                                                    <div className="price">${price}</div>
                                                    <div className="reason">{reason}</div>
                                                    <span className="glyphicons glyphicons-remove" onClick={() => {mp.trigger("policeFineCancel", key)}}></span>
                                                </div>
                                            )
                                        }) 
                                    }
                                    </div>
                            }
                            </div> : this.props.police.select === 3 ? <div id="call" className="scroll-block">
                            {
                                this.props.police.update.call === false ?  <div>
                                    {
                                        this.props.police.callList.length === 0 ? <div className="alert">Вызовов нет</div> :
                                        this.props.police.callList.map(([name, reason, distance], key) => {
                                            return (
                                                <div key={key}>
                                                    <div className="name">{name}</div>
                                                    <div className="reason">{reason}</div>
                                                    <div className="distance">{distance}м</div>
                                                    <span className="glyphicons glyphicons-ok" onClick={() => {mp.trigger("takePoliceCall", key)}}></span>
                                                    <span className="glyphicons glyphicons-remove" onClick={() => {mp.trigger("takePoliceCancel", key)}}></span>
                                                </div>
                                            )
                                        }) 
                                    }
                                    </div> : <div className="alert">Загрузка..</div>
                            }
                            </div> : this.props.police.select === 4 ? <div id="wanted" className=" scroll-block">
                            {
                                this.props.police.update.wanted === false ?  <div>
                                    {
                                        this.props.police.wantedList.length === 0 ? <div className="alert">Нарушителей нет</div> :
                                        this.props.police.wantedList.map(([name, star], key) => {
                                            return (
                                                <div key={key}>
                                                    <div className="name">{name}</div>
                                                    <div className="star">
                                                        <span className= {star >= 1 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                                        <span className= {star >= 2 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                                        <span className= {star >= 3 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                                        <span className= {star >= 4 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                                        <span className= {star >= 5 ? "glyphicons glyphicons-star" : "glyphicons glyphicons-star-empty" }></span>
                                                    </div>
                                                    <span className="glyphicons glyphicons-search" onClick={() => {mp.trigger("policeSearch", key)}}></span>
                                                    <span className="glyphicons glyphicons-remove" onClick={() => {mp.trigger("policeClear", key)}}></span>
                                                </div>
                                            )
                                        }) 
                                    }
                                    </div> : <div className="alert">Загрузка..</div>
                            }
                            </div> : this.props.police.select === 5 ? <div id="member" className=" scroll-block">
                            {
                                this.props.police.update.member === false ?  <div>
                                {
                                    this.props.police.memberList.length === 0 ? <div className="alert">Сотрудников нет</div> :
                                    this.props.police.memberList.map(([name, rank], key) => {
                                        return (
                                            <div key={key}>
                                                <div style={{width: this.props.police.leader ? "290px" : "452px"}} className="name">{name}</div>
                                                <div className="rank">{police.rank[rank-1]}</div>
                                                {
                                                    this.props.police.leader ? <div>
                                                        <span className="glyphicons glyphicons-plus" onClick={() => {mp.trigger("policeUpRank", key)}}></span>
                                                        <span className="glyphicons glyphicons-minus" onClick={() => {mp.trigger("policeDownRank", key)}}></span>
                                                        <span className="glyphicons glyphicons-user-remove" onClick={() => {mp.trigger("policeRemove", key)}}></span>
                                                    </div> : <div></div>
                                                }
                                            </div>
                                        )
                                    }) 
                                }
                                </div> : <div className="alert">Загрузка..</div>
                            }                            
                            </div> : this.props.police.select === 6 ? <div id="stats" className=" scroll-block">
                            {
                                this.props.police.update.stats === false ?  <div>
                                {
                                    this.props.police.statistics.length === 0 ? <div className="alert">Данных нет.</div> :
                                    this.props.police.statistics.map(([title, stat], key) => {
                                        return (
                                            <div key={key}>
                                                <div className="title">{title}</div>
                                                <div className="stat">{stat}</div>
                                            </div>
                                        )
                                    }) 
                                }
                                </div> : <div className="alert">Загрузка..</div>
                            }                                
                            </div>

                    :   <div></div>
                    }
                </div>
            </div>
        )
    }
}

export default connect(
    state => ({
        police: state.police,
    }),
    dispatch => ({
        setSelect: (select) => {
            dispatch({ type: 'POLICE_SELECT', select});
        },
        setStar: (star) => {
            dispatch({ type: 'POLICE_STAR', star});
        },
        setName: (name) => {
            dispatch({ type: 'POLICE_NAME', name});
        },
        setSelectName: (selectName) => {
            dispatch({ type: 'POLICE_SELECT_NAMES', selectName});
        },
        setUpdateStatus: (name, status) => {
            if(name === "name") mp.trigger("updatePoliceNames"); 
            else if(name === "call") mp.trigger("updatePoliceCall"); 
            else if(name === "wanted") mp.trigger("updatePoliceWanted"); 
            else if(name === "member") mp.trigger("updatePoliceMembers"); 
            else if(name === "stats") mp.trigger("updatePoliceStats"); 
            dispatch({ type: 'POLICE_UPDATE_STATUS', name, status});
        },
        setInfo: (name, star, fine) => {
            dispatch({ type: 'POLICE_SET_INFO', name, star, fine});
        },
    })
)(Police);


