var initial = {
    focus: 0,
    select: 0,
    focusItem: 0,
    selectItem: 0,    
    listPlayers: [
  /*  [1, "dukker", "255,255,255,255" , 54],
    [2, "test", "255,0,255,255" , 514]*/
    ],
    profile: {
        floor: 0,
        name: null,
        exp: 0,
        deaths: 0,
        kills: 0,
        offense: 0,
        faction: 0,
        rank: 0,
        job: 0,
    },
    items: {
 /*       phone: 555,
        canister: 1,
        key: 1,
        drugs: 444,
        material: 1000,
        money: 1,
        lic_car: true,
        lic_fly: false,
        lic_gun: false,
        lic_biz: true,
        lic_water: true,
        subsidy: 500,
        repr: 1	*/
    }
}

export default (state = initial, action) => {
    if (action.type === 'CONTROL_DATA_SET') {
        return {
            ...state, 
            listPlayers: action.players,
            profile: action.profile,
            items: action.items
        }
    } else if (action.type === 'CONTROL_FOCUS_MENU') {
        return {
            ...state, 
            focus: action.focus
        }       
    } else if (action.type === 'CONTROL_SELECT_MENU') {
        return {
            ...state, 
            select: action.select,
            focusItem: 0,
            selectItem: 0,  
        } 
    } else if (action.type === 'CONTROL_FOCUS_ITEM') {
        return {
            ...state, 
            focusItem: action.focusItem
        }       
    } else if (action.type === 'CONTROL_SELECT_ITEM') {
        return {
            ...state, 
            selectItem: action.selectItem
        } 
    }
    return state;
}


