import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/menu.css';

import API from'./api.js';

//import mp from'./mp.js';

window.setMenu = (title, list) => {
    API.store.dispatch({ type: 'MENU_SET', title, list: JSON.parse(list)});
    window.setGui("menu");
  }

class Menu extends Component {
    constructor(props) {
        super(props)
        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleClick() {
        if(this.props.menu.select < 0) return;
        mp.trigger("select", this.props.menu.select);
        window.setGui(null);
    }

    handleKeyUp(event) {
        if(event.keyCode === 38)
        {
            event.preventDefault();
            this.props.selectMenu(API.leftSlider(this.props.menu.list.length-1, this.props.menu.select))
        } 
        else if(event.keyCode === 40)
        {
            event.preventDefault();
            this.props.selectMenu(API.rightSlider(this.props.menu.list.length-1, this.props.menu.select))
        }		
        else if(event.keyCode === 13) 
        {
            event.preventDefault();
            this.handleClick();
        }        
    }

    render() { 
        return (
            <div id="menu">
                <div className="header">{this.props.menu.title ? this.props.menu.title : "" }</div>
                <ul className="scroll-block">
                {
                    this.props.menu.list.map((item, key) => {
                        return (
                            <li key = {key} 
                                className={this.props.menu.select === key ? "active" : ""}  
                                onMouseOver={() => {this.props.selectMenu(key)}}
                                onClick={this.handleClick} 
                                dangerouslySetInnerHTML={{__html: item}}
                            ></li> 
                        )
                    }) 
                }
                </ul>
            </div>
        )
    }
}

export default connect(
    state => ({
        menu: state.menu
    }),
    dispatch => ({    
        selectMenu: (select) => {
            dispatch({ type: 'MENU_SELECT', select});
        },
    })
)(Menu);


