var initial = {
    speed: 0,
    fuel: 0,
    engine: true,
    lock: true
}

export default (state = initial, action) => {
    if (action.type === 'SPEEDOMETER_SPEED_SET') {
        return {
             ...state,
             speed: action.speed,
        }
    } else if (action.type === 'SPEEDOMETER_FUEL_SET') {
            return {
                 ...state,
                 fuel: action.fuel,
            }
    } else if (action.type === 'SPEEDOMETER_ENGINE_SET') {
                return {
                     ...state,
                     engine: action.engine,
                }
    } else if (action.type === 'SPEEDOMETER_LOCK_SET') {
                return {
                     ...state,
                     lock: action.lock,
                }
    }  
    return state;
}


