import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/interaction.css';

import { interaction } from'./config.js';

import API from'./api.js';

//import mp from'./mp.js';

class Interaction extends Component {
    constructor(props) {
        super(props)

        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.handleClick = this.handleClick.bind(this)

        window.setRaycastFocus = this.props.setRaycastFocus
    } 
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
    }

    handleKeyUp(event) {
        if(event.keyCode === 13 && this.props.gui === "interaction") {
            event.preventDefault();
            this.handleClick();
        } else if(event.keyCode === 27 && this.props.gui === "interaction") {
            event.preventDefault();
            window.setGui(null)
        } else if(event.keyCode === 38 && this.props.gui === "interaction") {
            event.preventDefault();
            this.props.selectEtem(API.leftSlider(this.props.interaction.list.length-1, this.props.interaction.select))
        } 
        else if(event.keyCode === 40 && this.props.gui === "interaction") {
            event.preventDefault();
            this.props.selectEtem(API.rightSlider(this.props.interaction.list.length-1, this.props.interaction.select))
        }	
    }

    handleClick(){
        if(this.props.interaction.select === -1) return;
        mp.trigger("interaction", this.props.interaction.list[this.props.interaction.select]);
        window.setGui(null);
    }

    render() {

        return (
            <div id="interaction">
                {
                    this.props.gui === "interaction" ? <div className="menu">
                        <div className="header">Взаимодейтвие</div>
                        <ul className="scroll-block">
                        {
                            this.props.interaction.list.map((trigger, key) => {
                                return (
                                    <li key = {key} 
                                        className={this.props.interaction.select === key ? "active" : ""}  
                                        onMouseOver={() => {this.props.selectEtem(key)}}
                                        onClick={this.handleClick} 
                                        dangerouslySetInnerHTML={{__html: interaction.triggers[trigger]}}
                                    ></li> 
                                )
                            }) 
                        }
                        </ul>
                    </div> : this.props.gui === null ? <div className={this.props.interaction.raycast ? "cursor focus" : "cursor"}></div> : <div></div>
                }

            </div>
        )
    }
}

export default connect(
    state => ({
        interaction: state.interaction,
        gui: state.gui.open
    }),
    dispatch => ({
        setRaycastFocus: (raycast) => {
            dispatch({ type: 'INTERCACTION_RAYCAST', raycast});
        },
        selectEtem: (select) => {
            dispatch({ type: 'INTERCACTION_SELECT', select});
        },
        setList: (list) => {
            dispatch({ type: 'INTERCACTION_SET', list});
        },
    })
)(Interaction);


