import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/atm.css';
import './css/glyphicons.min.css';

import API from'./api.js'; //api

//import mp from'./mp.js';

window.setAtm = (balance) => {
    API.store.dispatch({ type: 'ATM_BALANCE', balance });
  }

class Atm extends Component {
    constructor(props) {
        super(props)
        this.handleAgo = this.handleAgo.bind(this)
        this.handleRun = this.handleRun.bind(this)
        this.handleKeyUp = this.handleKeyUp.bind(this)
    } 
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
    }
    componentWillUnmount() {
        document.removeEventListener("keyup", this.handleKeyUp)
        this.props.setSelect(-1);
        this.props.setFocus(-1);
    }

    handleKeyUp(event) {
        if(event.keyCode === 13 && this.props.atm.select === -1) {
            event.preventDefault();
            this.props.setSelect(this.props.atm.focus)
        } else if(event.keyCode === 37 && this.props.atm.select === -1) {
            event.preventDefault();
            this.props.setFocus(API.leftSlider(3, this.props.atm.focus))
        } else if(event.keyCode === 39 && this.props.atm.select === -1) {
            event.preventDefault();
            this.props.setFocus(API.rightSlider(3, this.props.atm.focus))
        } else if(event.keyCode === 13 && this.props.atm.select !== -1) {
            event.preventDefault();
            if(this.props.atm.focus === 0) this.handleAgo(); else if(this.props.atm.focus === 1) this.handleRun(); 
        } else if(event.keyCode === 37 && this.props.atm.select !== -1) {
            event.preventDefault();
            this.props.setFocus(API.leftSlider(1, this.props.atm.focus))
        } else if(event.keyCode === 39 && this.props.atm.select !== -1) {
            event.preventDefault();
            this.props.setFocus(API.rightSlider(1, this.props.atm.focus))
        } else if(event.keyCode === 27) {
            event.preventDefault();
            window.setGui(null)
        }
    }

    handleAgo() {
        this.props.setSelect(-1);
    }

    handleRun() {
        if(this.props.atm.select === 0) {
            mp.trigger("atmReplenish", this.inputAmount.value)
        } else if(this.props.atm.select === 1) {
            mp.trigger("atmTransfer", this.inputId.value, this.inputAmount.value)
        } else if(this.props.atm.select === 2) {
            mp.trigger("atmOutput", this.inputAmount.value)
        } else if(this.props.atm.select === 3) {
            mp.trigger("atmPayment", this.inputAmount.value)    
        }
        this.props.setSelect(-1);
    }

    render() { 
        let atm_menu = [
            ["glyphicons glyphicons-arrow-down", "Пополнить"], // Клавиши банкомата
            ["glyphicons glyphicons-new-window", "Перевести"], // Клавиши банкомата
            ["glyphicons glyphicons-arrow-up", "Вывести"], // Клавиши банкомата
            ["glyphicons glyphicons-money", "Оплатить"], // Клавиши банкомата
        ]
        return (
            <div id="atm"><button type="button" className="close" onClick={() => { window.setGui(null)} }>×</button>
                <div className="atm_body">
                    {
                        this.props.atm.select === 0 ? 
                            <div>
                                <div className="atm_title">Пополнить</div>
                                <input type="text" placeholder="Сумма" ref={(input) => { if(input) { this.inputAmount =  input; input.focus()}}} /><br />
                                <button type="button" onMouseOver={() => {this.props.setFocus(0)}} onClick={this.handleAgo.bind(this)} className={this.props.atm.focus === 0 ? "focus" : ""}><span className="glyphicons glyphicons-unshare"></span> Назад</button>
                                <button type="button" onMouseOver={() => {this.props.setFocus(1)}} onClick={this.handleRun.bind(this)} className={this.props.atm.focus === 1 ? "focus" : ""}><span className="glyphicons glyphicons-share"></span> Выполнить</button>
                            </div> :
                        this.props.atm.select === 1 ? 
                            <div>
                                <div className="atm_title">Перевести</div>
                                <input type="text" placeholder="Индикиатор клиента" ref={(input) => { if(input) { this.inputId=  input; input.focus()}}} /><br />
                                <input type="text" placeholder="Сумма" ref={(input) => { if(input) { this.inputAmount=  input; }}} /><br />
                                <button type="button" onMouseOver={() => {this.props.setFocus(0)}} onClick={this.handleAgo} className={this.props.atm.focus === 0 ? "focus" : ""}><span className="glyphicons glyphicons-unshare"></span> Назад</button>
                                <button type="button" onMouseOver={() => {this.props.setFocus(1)}} onClick={this.handleRun} className={this.props.atm.focus === 1 ? "focus" : ""}><span className="glyphicons glyphicons-share"></span> Выполнить</button>
                            </div> :
                        this.props.atm.select === 2 ? 
                            <div>
                                <div className="atm_title">Вывести</div>
                                <input type="text" placeholder="Сумма" ref={(input) => { if(input) { this.inputAmount=  input; input.focus()}}} /><br />
                                <button type="button" onMouseOver={() => {this.props.setFocus(0)}} onClick={this.handleAgo} className={this.props.atm.focus === 0 ? "focus" : ""}><span className="glyphicons glyphicons-unshare"></span> Назад</button>
                                <button type="button" onMouseOver={() => {this.props.setFocus(1)}} onClick={this.handleRun} className={this.props.atm.focus === 1 ? "focus" : ""}><span className="glyphicons glyphicons-share"></span> Выполнить</button>
                                
                            </div> :
                        this.props.atm.select === 3 ? 
                            <div>
                                <div className="atm_title">Оплатить</div>
                                <p>Пополнить мобильный телефон</p>
                                <input type="text" placeholder="Сумма" ref={(input) => { if(input) { this.inputAmount=  input; input.focus()}}} /><br />
                                <button type="button" onMouseOver={() => {this.props.setFocus(0)}} onClick={this.handleAgo} className={this.props.atm.focus === 0 ? "focus" : ""}><span className="glyphicons glyphicons-unshare"></span> Назад</button>
                                <button type="button" onMouseOver={() => {this.props.setFocus(1)}} onClick={this.handleRun} className={this.props.atm.focus === 1 ? "focus" : ""}><span className="glyphicons glyphicons-share"></span> Выполнить</button>

                            </div> :
                            <div>
                                <div className="atm_title">Ваш баланс: <span className="glyphicons glyphicons-usd"></span> {this.props.atm.balance}</div>
                                {
                                    atm_menu.map(([icon,name], key) => {
                                        return (
                                            <div key={key} onMouseOver={() => {this.props.setFocus(key)}} onClick={() => {this.props.setSelect(key)}} className={this.props.atm.focus === key ? "atm_item focus" : "atm_item"}>
                                                <span className={icon}></span>
                                                <p>{name}</p>
                                            </div>                            
                                        )
                                    })
                                }
                            </div>
                    }
                </div>
            </div>
        )
    }
}

export default connect(
    state => ({
        atm: state.atm
    }),
    dispatch => ({
        setFocus: (focus) => {
            dispatch({ type: 'ATM_FOCUS', focus });
        },
        setSelect: (select) => {
            dispatch({ type: 'ATM_SELECT', select });
        }       
    })
)(Atm);


