var initial = {
    balance: 0,
    select: -1,
    focus: -1,
}

export default (state = initial, action) => {
    if (action.type === 'ATM_BALANCE') {
        return {
             ...state,
            balance: action.balance,
        }           
    }  else if (action.type === 'ATM_FOCUS') {
        return {
             ...state,
            focus: action.focus,
        }           
    }  else if (action.type === 'ATM_SELECT') {
        return {
             ...state,
            select: action.select,
            focus: -1
        }           
    }  
    return state;
}


