import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/chat.css';

//import mp from'./mp.js';

class Chat extends Component {

    constructor(props) {
        super(props)
        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.handleKeyDown = this.handleKeyDown.bind(this)

        window.chatAPI = {}
        window.chatAPI.push = this.insertMessageToChat.bind(this)
        window.chatAPI.clear = this.props.clearChat
        window.chatAPI.show = this.props.showChat
        window.chatAPI.activate = this.props.activeInput
        window.chatAPI.send = this.sendMessage.bind(this)
        window.chatAPI.value = this.props.valueInput

        this.label = ["/me ", "/do ", "/try ", "/b ", "/s ", "/w "]
        
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
        document.addEventListener("keydown", this.handleKeyDown);
    }
    componentWillUnmount() {  
        document.removeEventListener("keyup", this.handleKeyUp)
        document.removeEventListener("keydown", this.handleKeyDown)        
    }

    insertMessageToChat(str) {
        this.props.addMessage(str);
        const scrollHeight = this.messageList.scrollHeight;
        const height = this.messageList.clientHeight;
        const maxScrollTop = scrollHeight - height;
        this.messageList.scrollTop = maxScrollTop > 0 ? maxScrollTop : 0;
    }

    sendMessage(value) {
        if(value.length > 0) {
            this.props.addHistoryMessage(value);
            if(value[0] === "/") {
                value = value.substr(1);
                if(value.length > 0) mp.invoke("command", value);
            } else { mp.invoke("chatMessage", value); }
            this.props.valueInput("")
        }
    }

    handleKeyDown(event) {
        if(event.keyCode === 9 && this.props.chat.active) {
            event.preventDefault();
            if(this.props.chat.labelPosition === this.label.length-1) {
                this.props.upLablePos(-1);
                this.props.valueInput("");
            } else {
                this.props.upLablePos(parseInt(this.props.chat.labelPosition, 10) + 1);
                this.props.valueInput(this.label[this.props.chat.labelPosition]);
            }
        }
    }

    handleKeyUp(event) {
        if(event.keyCode === 13 && this.props.chat.active) {
            event.preventDefault();
            this.sendMessage(this.inputChat.value)
            this.props.activeInput(false);
        } else if(event.keyCode === 27 && this.props.chat.active) {
            event.preventDefault();
            this.props.activeInput(false);
        } else if(event.keyCode === 40 && this.props.chat.active && this.props.chat.historyPosition > -1) {
            if(this.props.chat.historyPosition === 0) {
                this.props.updatePosition(-1);
				this.props.valueInput("");
			} else {
                this.props.updatePosition(this.props.chat.historyPosition - 1);
                this.props.valueInput(this.props.chat.historyMessages[this.props.chat.historyPosition]);
            }
        } else if(event.keyCode === 38 && this.props.chat.active && this.props.chat.historyPosition < this.props.chat.historyMessages.length-1) {
             this.props.updatePosition(this.props.chat.historyPosition + 1);
             this.props.valueInput(this.props.chat.historyMessages[this.props.chat.historyPosition]);
        }      
    }

    render() { 
        return (
                <div id = "chat">
                    <div id="chat_messages" className={this.props.chat.active ? "scroll-block" : ""} ref={(div) => {this.messageList = div}}>
                        <ul>
                        {
                            this.props.chat.show ? this.props.chat.messages.map((item, key) => {
                                return (
                                    <li key = {key} dangerouslySetInnerHTML={{__html: item}}></li> 
                                )
                            }) : ""
                        }
                        </ul>
                    </div>    
                    <div>
                    {
                        this.props.chat.active ?  <input type = "text" ref={(input) => { if(input) { this.inputChat = input; input.focus()}}} value={this.props.chat.value}  onChange = {(e) => {this.props.valueInput(e.target.value)}}/> : ""
                    }
                    </div>
                </div>
        )
    }
}

export default connect(
    state => ({
        chat: state.chat
    }),
    dispatch => ({
        activeInput: (active) => {
            mp.invoke("focus", active);
            dispatch({ type: 'CHAT_ACTIVATE', active });
        },
        valueInput: (value) => {
            dispatch({ type: 'CHAT_VALUE', value });
        },        
        addMessage: (message) => {
            dispatch({ type: 'CHAT_MESSAGES', message });
        },
        clearChat: () => {
           dispatch({ type: 'CHAT_MESSAGES_CLEAR'});
        },
        showChat: (toggle) => {
         dispatch({ type: 'CHAT_SHOW', toggle });
        },        
        addHistoryMessage: (history) => {
            dispatch({ type: 'CHAT_HISTORY_MESSAGES', history });
        },       
        updatePosition: (position) => {
           dispatch({ type: 'CHAT_HISTORY_POSITION', position});
        },
        upLablePos: (position) => {
            dispatch({ type: 'CHAT_LABEL_POSITION', position});
        }
    })
)(Chat);


