import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import './css/alert.css';

class Alert extends Component {
    constructor(props) {
        super(props)

        window.setAlert = this.setAlert.bind(this)
    } 

    setAlert (type,text) { 
        this.props.addAlert(type, unescape(text));
        setTimeout(() => { this.props.removeAlert() }, 5000);
    }

    render() { 
        return (
            <div id="alert">
                <ul>
                    <ReactCSSTransitionGroup   
                        transitionName="animation"
                        transitionEnterTimeout={800}
                        transitionLeaveTimeout={800}> 
                        {
                            Object.keys(this.props.alert.list).map((key) => {
                                let [type,text] = this.props.alert.list[key]
                                return (                            
                                    <li className={type} key={key} dangerouslySetInnerHTML={{__html: text}}></li> 
                                )
                            })
                        }       
                    </ReactCSSTransitionGroup>
                </ul>
            </div>
        )
    }
}

export default connect(
    state => ({
        alert: state.alert
    }),
    dispatch => ({
        addAlert: (type, text) => {
            dispatch({type: 'ALERT_ADD_TEXT', typeAlert: type, text: text});
        },
        removeAlert: () => {
            dispatch({ type: 'ALERT_REMOVE'});
        },
    })
)(Alert);


