var initial = {
    focusApp: -1,
    alert: null,
    alertTimer: 0,
    openApp: null,
    windowApp: null,
    dialNumber: "",
    signal: true,
    presence: true,
    historyCall: [],
    historyMessage: {
        555: ["Франческо", [["Добро пожаловать в Лос Сантос","left"],["Кто ты?","right"],["Я твой гид. Будет что-то нужно - напишу","left"]]],
        777: ["Банк LS", [["Ваша карта была создана","left"]]]
    },
    testmsgs: {
        555: ["Франческо", [["Не могу связаться с тобой сейчас"]]]
    },
    apps: ["phone","signal","message","balance","store","911","gps","bank","info"]
}

export default (state = initial, action) => {
    if (action.type === 'PHONE_FOCUS_APP') {
        return {
             ...state,
             focusApp: action.focus,
        }           
    }  else if (action.type === 'PHONE_OPEN_APP') {
        return {
             ...state,
             openApp: action.open,
             windowApp: null,
             focusApp: -1
        }           
    }  else if (action.type === 'PHONE_OPEN_WINDOW_APP') {
        return {
             ...state,
             windowApp: action.window,
        }     
    }  else if (action.type === 'PHONE_DIAL_NUMBER') {
        let dialNumber = String(state.dialNumber)+String(action.number);
        return {
             ...state,
             dialNumber: dialNumber,
        }    
    }  else if (action.type === 'PHONE_DIAL_DELETE') {
        let dialNumber = state.dialNumber.slice(0, -1);
        return {
             ...state,
             dialNumber: dialNumber,
        }     
    }  else if (action.type === 'PHONE_DIAL_CLEAN') {
        return {
             ...state,
             dialNumber: "",
        }    
    }  else if (action.type === 'PHONE_ALERT') {
        clearInterval(state.alertTimer);
        return {
             ...state,
             alert: action.text,
             alertTimer: action.timer,
        }    
    }  
    return state;
}


