var initial = {
    buttonLeft: "",
    buttonRight: "",
    text: "",
    title: "",
    select: -1,
    input: ""
}

export default (state = initial, action) => {
    if (action.type === 'DIALOG_SET') {
        return {
             ...state,
            buttonLeft: action.buttonLeft,
            buttonRight: action.buttonRight,
            input: action.input,
            text: action.text,
            title: action.title,
            select: action.select,
        }
    } else if(action.type === 'DIALOG_SELECT') {
        return {
             ...state,
            select: action.select,
        }
    } else if(action.type === 'DIALOG_DELITE') {
        return {
             ...state, initial
        }
    }    
    return state;
}


