var initial = {
    open: null,
    speed: false,
    cursor: false,
}

export default (state = initial, action) => {
    if (action.type === 'GUI_SET') {
        return {
             ...state,
            open: action.gui,
        }    
    } else if (action.type === 'GUI_SET_SPEED') {
        return {
             ...state,
             speed: action.speed,
        }  
    } else if (action.type === 'GUI_CURSOR') {
        return {
             ...state,
             cursor: action.cursor,
        }   
    } 
    return state;
}


