var initial = {
    select: 0,
    star: 1,
    name: {
        select: "",
        list: [],
        filter:  []
    },
    update: {
        name: true,
        call: false,
        wanted: true,
        member: true,
        stats: true
    },
    wantedList: [],
    leader: false,
    info: {
        name: "",
        star: 0,
        fine: [] 
    },
    memberList: [],
    callList: [],
    statistics: [],
}

export default (state = initial, action) => {
    if (action.type === 'POLICE_SELECT') {
        return {
             ...state,
             select: action.select,
        }           
    } else if (action.type === 'POLICE_STAR') {
        return {
             ...state,
             star: action.star,
        } 
    } else if (action.type === 'POLICE_SET_LIST_NAME') {
        let name = state.name;
        name.list = action.list;
        name.filter = action.list;
        name.select = "";
        let update = state.update;
        update.name = false;
        return {
             ...state,
             name: name,
             update: update
        }  
    } else if (action.type === 'POLICE_SET_LIST_CALL') {
        let update = state.update;
        update.call = false;       
        return {
             ...state,
             callList: action.list,
             update: update
        } 
    } else if (action.type === 'POLICE_SET_MEMBER') {
        let update = state.update;
        update.member = false;       
        return {
             ...state,
             leader: action.leader,
             memberList: action.list,
             update: update
        }  
    } else if (action.type === 'POLICE_SET_INFO') {
        let info = state.info;
        info = {
            name: action.name,
            star: action.star,
            fine: action.fine,
        }
        return {
             ...state,
             info: info,
        }  
    } else if (action.type === 'POLICE_SET_WANTED') {
        let update = state.update;
        update.wanted = false;       
        return {
             ...state,
             wantedList: action.list,
             update: update
        }  
    } else if (action.type === 'POLICE_SET_STATS') {
        let update = state.update;
        update.stats = false;       
        return {
             ...state,
             statistics: action.list,
             update: update
        }        
    } else if (action.type === 'POLICE_NAME') {
        let name = state.name;
        name.select = action.name;
        return {
             ...state,
             name: name
        }        
    } else if (action.type === 'POLICE_UPDATE_STATUS') {
        let update = state.update;
        update[action.name] = action.status;
        return {
             ...state,
             update: update
        }    
    } else if (action.type === 'POLICE_SELECT_NAMES') {
        let name = state.name;
        name.filter = action.selectName;
        return {
             ...state,
             name: name,
        }           
    }
    return state;
}


