var initial = {
    select: -1,
    raycast: false,
    list: []
}

export default (state = initial, action) => {
    if (action.type === 'INTERCACTION_SELECT') {
        return {
            ...state,
            select: action.select,
        }
    } else if (action.type === 'INTERCACTION_RAYCAST') {
        return {
            ...state,
            raycast: action.raycast,
        }
    } else if (action.type === 'INTERCACTION_SET') {
        return {
            ...state,
            select: -1,
            list: action.list,
        }
    }
    return state;
}


