import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/auth.css';
import './css/glyphicons.min.css';

import API from'./api.js';
import { personage } from'./config.js';

//import mp from'./mp.js';

class Personage extends Component {

    constructor(props) {
        super(props)
        this.handleKeyUp = this.handleKeyUp.bind(this)
        this.handleKeyIgnoring = this.handleKeyIgnoring.bind(this)
        
        this.handleReg = this.handleReg.bind(this)
        
        this.floor = ["Мужской", "Женский"];
        this.face = personage.face;
        
    }
    componentDidMount() {
        document.addEventListener("keyup", this.handleKeyUp);
        document.addEventListener("keypress", this.handleKeyIgnoring);
        document.addEventListener("keydown", this.handleKeyIgnoring);
        if(this.props.auth.cam !== "camSkinAuth") {
            mp.trigger("camSkinAuth");
            this.props.setCam("camSkinAuth")
        }
    }
    componentWillUnmount() {  
        document.removeEventListener("keyup", this.handleKeyUp)
        document.removeEventListener("keypress", this.handleKeyIgnoring)
        document.removeEventListener("keydown", this.handleKeyIgnoring)        
    }

    handleKeyIgnoring(event) {
        if(event.keyCode === 38 || event.keyCode === 40) {
            event.preventDefault();
        }

    }

    handleKeyUp(event) {
        if(event.keyCode === 13 && this.props.auth.section === 4) {
            event.preventDefault();
            this.handleReg();
        } else if((event.keyCode === 37  || event.keyCode === 39) && this.props.auth.section === 0) {
            this.props.setFloor();
        } else if(event.keyCode === 37 && this.props.auth.section === 1) {
            if(this.props.auth.item === 0 || this.props.auth.item === 1) {
                this.props.faceLeft(personage.face[this.props.auth.item].length-1, this.props.auth.face[this.props.auth.item])
            }
        } else if(event.keyCode === 39 && this.props.auth.section === 1) {
            if(this.props.auth.item === 0 || this.props.auth.item === 1) {
                this.props.faceRight(personage.face[this.props.auth.item].length-1, this.props.auth.face[this.props.auth.item])
            }                      
        } else if(event.keyCode === 38 && this.props.auth.section === 0) {
            this.props.setSection(5)
        } else if(event.keyCode === 38 && this.props.auth.section === 5) {
            this.props.setSection(4)
            this.props.setItem(3)
        } else if(event.keyCode === 38 && this.props.auth.section === 4 && this.props.auth.item !== 0) {
            this.props.setItem(this.props.auth.item-1)
        } else if(event.keyCode === 38 && this.props.auth.section === 4 && this.props.auth.item === 0) {
            this.props.setSection(3)
            this.props.setItem(personage.appearance.length-1)    
        } else if(event.keyCode === 38 && this.props.auth.section === 3 && this.props.auth.item !== 0) {
            this.props.setItem(this.props.auth.item-1)                        
        } else if(event.keyCode === 38 && this.props.auth.section === 3 && this.props.auth.item === 0) {
            this.props.setSection(2)
            this.props.setItem(personage.features.length-1)            
        } else if(event.keyCode === 38 && this.props.auth.section === 2 && this.props.auth.item !== 0) {
            this.props.setItem(this.props.auth.item-1)   
        } else if(event.keyCode === 38 && this.props.auth.section === 2 && this.props.auth.item === 0) {
            this.props.setSection(1)
            this.props.setItem(3)
        } else if(event.keyCode === 38 && this.props.auth.section === 1 && this.props.auth.item !== 0) {
            this.props.setItem(this.props.auth.item-1)    
        } else if(event.keyCode === 38 && this.props.auth.section === 1 && this.props.auth.item === 0) {
            this.props.setSection(0)
        } else if(event.keyCode === 38 && this.props.auth.section === 0) {
            this.props.setSection(5)
        } else if(event.keyCode === 40 && this.props.auth.section === 0) { 
            this.props.setSection(1)
            this.props.setItem(0)
        } else if(event.keyCode === 40 && this.props.auth.section === 1 && this.props.auth.item !== 3) { 
            this.props.setItem(this.props.auth.item + 1)
        } else if(event.keyCode === 40 && this.props.auth.section === 1 && this.props.auth.item === 3) {
            this.props.setSection(2)
            this.props.setItem(0)
        } else if(event.keyCode === 40 && this.props.auth.section === 2 && this.props.auth.item !== personage.features.length-1) {
            this.props.setItem(this.props.auth.item + 1); 
        } else if(event.keyCode === 40 && this.props.auth.section === 2 && this.props.auth.item === personage.features.length-1) {
            this.props.setSection(3);
            this.props.setItem(0)
        } else if(event.keyCode === 40 && this.props.auth.section === 3 && this.props.auth.item !== personage.appearance.length-1) {
            this.props.setItem(this.props.auth.item + 1);
        } else if(event.keyCode === 40 && this.props.auth.section === 3 && this.props.auth.item === personage.appearance.length-1) {
            this.props.setSection(4);
        } else if(event.keyCode === 40 && this.props.auth.section === 4 && this.props.auth.item !== 5) {
            this.props.setItem(this.props.auth.item + 1);
        } else if(event.keyCode === 40 && this.props.auth.section === 4 && this.props.auth.item === 5) {
            this.props.setSection(5);
        } else if(event.keyCode === 40 && this.props.auth.section === 5) {
            this.props.setSection(0);
        }   
    }
    handleReg() {
        mp.trigger("regAuth", this.props.auth.name, this.props.auth.password, this.props.auth.email)
        window.setGui(null);
    }
    render() { 
        return (
            <div id="personage">
                <div className="header">Редактор персонажа</div>
                <div className={this.props.auth.focus === 0 || this.props.auth.section === 0 ? "item section scroll-block" : "item"} onClick={() => {this.props.setSection(0)}} onMouseOver={() => {this.props.setFocus(0)}} >
                    <div className="left">Пол</div>
                    <div className="right">
                        <span className="glyphicons glyphicons-chevron-left" onClick={() => {this.props.setFloor()}} ></span>
                        &nbsp;{this.floor[this.props.auth.floor]}&nbsp;
                        <span className="glyphicons glyphicons-chevron-right"onClick={() => {this.props.setFloor()}} ></span>
                    </div>
                </div>
                <div className={this.props.auth.focus === 1 || this.props.auth.section === 1 ? "item section scroll-block" : "item"} onClick={() => {this.props.setSection(1)}} onMouseOver={() => {this.props.setFocus(1)}}>
                    {
                    this.props.auth.section !== 1 ? <span id="heredity-header">Наследственность</span> :
                    <div id="heredity-body">
                        <div className={this.props.auth.item === 0 ? "block focus" : "block" } onMouseOver={() => {this.props.setItem(0)}}>
                            <span onClick={() => {this.props.faceLeft(personage.face[0].length-1, this.props.auth.face[this.props.auth.item])}} className="glyphicons glyphicons-chevron-left"></span>
                            <img src={require(`./img/face/${this.face[0][this.props.auth.face[0]]}.png`)} alt="father"/>
                            <span onClick={() => {this.props.faceRight(personage.face[0].length-1, this.props.auth.face[this.props.auth.item])}} className="glyphicons glyphicons-chevron-right"></span>
                        </div>
                        <div className={this.props.auth.item === 1 ? "block focus" : "block" } onMouseOver={() => {this.props.setItem(1)}}>
                            <span onClick={() => {this.props.faceLeft(personage.face[1].length-1, this.props.auth.face[this.props.auth.item])}} className="glyphicons glyphicons-chevron-left"></span>
                            <img src={require(`./img/face/${this.face[1][this.props.auth.face[1]]}.png`)} alt="mother"/>
                            <span  onClick={() => {this.props.faceRight(personage.face[1].length-1, this.props.auth.face[this.props.auth.item])}}  className="glyphicons glyphicons-chevron-right"></span>
                        </div>
                        <div className={this.props.auth.item === 2 ? "focus range" : "range" } onMouseOver={() => {this.props.setItem(2)}}>
                            <label>Цвет кожи&nbsp;</label>
                            <input ref={(input) => { if(input && this.props.auth.item === 2) { input.focus()}}} type="range" min="0" max="1.0" step="0.1" onChange = {(e) => {this.props.setSkin(e.target.value)}} value={this.props.auth.skin}/>
                        </div>
                        <div className={this.props.auth.item === 3 ? "focus range" : "range" } onMouseOver={() => {this.props.setItem(3)}}>
                            <label>Сходство&nbsp;&nbsp;</label>
                            <input ref={(input) => { if(input && this.props.auth.item === 3) { input.focus()}}} type="range" min="0" max="1.0" step="0.1" onChange = {(e) => {this.props.setShape(e.target.value)}} value={this.props.auth.shape}/>
                        </div>
                    </div>
                    }
                </div>
                <div className={this.props.auth.focus === 2 || this.props.auth.section === 2 ? "item section scroll-block" : "item"} onClick={() => {this.props.setSection(2)}} onMouseOver={() => {this.props.setFocus(2)}}>
                    {
                    this.props.auth.section !== 2 ? <div id="features-header">Характеристики</div> :
                    <div id="features-body">
                        {
                            personage.features.map(([id,  name, [min, max]], key) => {
                                return (
                                    <div key={key} className={this.props.auth.item === key ? "focus" : "" } onMouseOver={() => {this.props.setItem(key)}}>
                                        <div className="left">{name}</div>
                                        <div className="right">
                                            <div className="right">
                                                <input ref={(input) => { if(input && this.props.auth.item === key) { input.focus()}}} className={key === 5 ? "mini" : ""} type="range" min={min} max={max} step="0.1" onChange = {(e) => {this.props.setFeatures(e.target.value)}} value={this.props.auth.features[key]}/>
                                            </div> 
                                        </div>
                                    </div>
                                )
                            })     
                        }
                    </div>
                    }
                </div>
                <div className={this.props.auth.focus === 3 || this.props.auth.section === 3 ? "item section scroll-block" : "item"} onClick={() => {this.props.setSection(3)}} onMouseOver={() => { this.props.setFocus(3)}}>
                    {
                    this.props.auth.section !== 3 ? <div id="appearance-header">Внешность</div> :
                    <div id="appearance-body">
                        {
                            personage.appearance.map(([name, select], key) => {
                                return (
                                    <div key={key} className={this.props.auth.item === key ? "focus " : "" } onMouseOver={() => {this.props.setItem(key)}}>
                                        <div className="left">{name}</div>
                                        <div className="right">
                                            <input ref={(input) => { if(input && this.props.auth.item === key) { input.focus()}}} type="range" min="0" max={select[this.props.auth.floor]} step="1" onChange = {(e) => {this.props.setAppearance(e.target.value)}} value={this.props.auth.appearance[key]}/>
                                        </div>
                                    </div>
                                )
                            })     
                        }
                    </div>
                    }                        
                </div>
                <div className={this.props.auth.focus === 4 || this.props.auth.section === 4 ? "item section scroll-block" : "item"} onClick={() => {this.props.setSection(4)}} onMouseOver={() => { this.props.setFocus(4)}}>
                    {
                    this.props.auth.section !== 4 ? <div id="clothing-header">Одежда</div> :
                    <div id="clothing-body">
                            <div className={this.props.auth.item === 0 ? "focus" : "" } onMouseOver={() => {this.props.setItem(0)}}>
                                <div className="left">Торс</div>
                                <div className="right">
                                    <input ref={(input) => { if(input && this.props.auth.item === 0) { input.focus()}}} type="range" min="0" max={personage.tops[this.props.auth.floor].length-1} step="1" onChange = {(e) => {this.props.setClothing(e.target.value)}} value={this.props.auth.clothing[0]} />
                                </div>
                            </div>
                            <div className={this.props.auth.item === 1 ? "focus" : "" } onMouseOver={() => {this.props.setItem(1)}}>
                                <div className="left">Цвет торса</div>
                                <div className="right">
                                    <input ref={(input) => { if(input && this.props.auth.item === 1) { input.focus()}}} type="range" min="0" max={personage.tops[this.props.auth.floor][this.props.auth.clothing[0]][1].length-1} step="1" onChange = {(e) => {this.props.setClothing(e.target.value)}} value={this.props.auth.clothing[1]} />
                                </div>
                            </div>
                            <div className={this.props.auth.item === 2 ? "focus" : "" } onMouseOver={() => {this.props.setItem(2)}}>
                                <div className="left">Штаны</div>
                                <div className="right">
                                    <input ref={(input) => { if(input && this.props.auth.item === 2) { input.focus()}}} type="range" min="0" max={personage.feet[this.props.auth.floor].length-1} step="1" onChange = {(e) => {this.props.setClothing(e.target.value)}} value={this.props.auth.clothing[2]} />
                                </div>
                            </div> 
                             <div className={this.props.auth.item === 3 ? "focus" : "" } onMouseOver={() => {this.props.setItem(3)}}>
                                <div className="left">Цвет штанов</div>
                                <div className="right">
                                    <input ref={(input) => { if(input && this.props.auth.item === 3) { input.focus()}}} type="range" min="0" max="2" step="1" onChange = {(e) => {this.props.setClothing(e.target.value)}} value={this.props.auth.clothing[3]} />
                                </div>
                            </div>
                            <div className={this.props.auth.item === 4 ? "focus" : "" } onMouseOver={() => {this.props.setItem(4)}}>
                                <div className="left">Обувь</div>
                                <div className="right">
                                    <input ref={(input) => { if(input && this.props.auth.item === 4) { input.focus()}}} type="range" min="0" max={personage.legs[this.props.auth.floor].length-1} step="1" onChange = {(e) => {this.props.setClothing(e.target.value)}} value={this.props.auth.clothing[4]} />
                                </div>
                            </div>
                            <div className={this.props.auth.item === 5 ? "focus" : "" } onMouseOver={() => {this.props.setItem(5)}}>
                                <div className="left">Цвет обуви</div>
                                <div className="right">
                                    <input ref={(input) => { if(input && this.props.auth.item === 3) { input.focus()}}} type="range" min="0" max="2" step="1" onChange = {(e) => {this.props.setClothing(e.target.value)}} value={this.props.auth.clothing[5]} />
                                </div>
                            </div>   
                    </div>
                    }                        
                </div>
                <div className={this.props.auth.focus === 5 || this.props.auth.section === 5 ? "item section scroll-block" : "item"} onMouseOver={() => {this.props.setFocus(5)}} onClick={this.handleReg}>Сохранить</div>
                            
              </div>
        )
    }
}

export default connect(
  state => ({
    auth: state.auth
  }),
  dispatch => ({
    setFocus: (focus) => {
        dispatch({ type: 'AUTH_PERSONAGE_FOCUS', focus });
      }, 
    setSection: (section) => {
      dispatch({ type: 'AUTH_PERSONAGE_SECTION', section });
    },
    setItem: (item) => {
      dispatch({ type: 'AUTH_PERSONAGE_ITEM', item });
    },    
    setFloor: () => {
      dispatch({ type: 'AUTH_PERSONAGE_FLOOR'});
    },
    faceRight: (max, pick) => {
        dispatch({ type: 'AUTH_PERSONAGE_FACE', face: API.rightSlider(max, pick)});
    },  
    faceLeft: (max, pick) => {
        dispatch({ type: 'AUTH_PERSONAGE_FACE', face: API.leftSlider(max, pick)});
    },  
    setFeatures: (features) => {
        dispatch({ type: 'AUTH_PERSONAGE_FEATURES', features});
    },   
    setAppearance: (appearance) => {
        dispatch({ type: 'AUTH_PERSONAGE_APPEARANCE', appearance: parseInt(appearance,10) });
    },
    setClothing: (clothing) => {
        dispatch({ type: 'AUTH_PERSONAGE_CLOTHING', clothing: parseInt(clothing,10) });
    },
    setSkin: (skin) => {
      dispatch({ type: 'AUTH_PERSONAGE_SKIN', skin });
    },
    setShape: (shape) => {
      dispatch({ type: 'AUTH_PERSONAGE_SHAPE', shape });
    },    
    setCam: (cam) => {
        dispatch({ type: 'AUTH_PERSONAGE_CAM', cam });
      },  
  })
)(Personage);


