var initial = {
    active: false,
    show: false,
    value: "",
    messages: [],
    historyMessages: [],
    labelPosition: -1,
    historyPosition: -1
}

export default (state = initial, action) => {
    if (action.type === 'CHAT_MESSAGES') {
        let messages = state.messages;
        messages.push(action.message);
        if(messages.length > 50 ) messages.splice(1, 1);
        return {
             ...state,
            messages: messages,
        }           
    } else if (action.type === 'CHAT_ACTIVATE') {
        return {
             ...state,
            active: action.active,
            historyPosition: -1
        }
    } else if (action.type === 'CHAT_VALUE') {
        return {
             ...state,
            value: action.value,
        }
    } else if (action.type === 'CHAT_SHOW') {
        return {
             ...state,
            show: action.toggle,
        }
    } else if (action.type === 'CHAT_MESSAGES_CLEAR') {
        return {
             ...state,
            messages: [],
        }
    } else if (action.type === 'CHAT_HISTORY_MESSAGES') {
        let history = state.historyMessages;
        history.unshift(action.history);
        if(history.length > 50 ) history.pop();
        return {
             ...state,
            historyMessages: history,
        }
    } else if (action.type === 'CHAT_HISTORY_POSITION') {
        return {
             ...state,
            historyPosition: action.position,
        }
    } else if (action.type === 'CHAT_LABEL_POSITION') {
        return {
             ...state,
             labelPosition: action.position,
        }
    }   
    return state;
}


